import html2Canvas from 'html2canvas'
import JsPDF from 'jspdf'

export function resetSize(vm) {
  let imgWidth, imgHeight, barWidth, barHeight // 图片的宽度、高度，移动条的宽度、高度

  const parentWidth = vm.$el.parentNode.offsetWidth || window.offsetWidth
  const parentHeight = vm.$el.parentNode.offsetHeight || window.offsetHeight

  if (vm.imgSize.width.indexOf('%') !== -1) {
    imgWidth = (parseInt(this.imgSize.width) / 100) * parentWidth + 'px'
  } else {
    imgWidth = this.imgSize.width
  }

  if (vm.imgSize.height.indexOf('%') !== -1) {
    imgHeight = (parseInt(this.imgSize.height) / 100) * parentHeight + 'px'
  } else {
    imgHeight = this.imgSize.height
  }

  if (vm.barSize.width.indexOf('%') !== -1) {
    barWidth = (parseInt(this.barSize.width) / 100) * parentWidth + 'px'
  } else {
    barWidth = this.barSize.width
  }

  if (vm.barSize.height.indexOf('%') !== -1) {
    barHeight = (parseInt(this.barSize.height) / 100) * parentHeight + 'px'
  } else {
    barHeight = this.barSize.height
  }

  return { imgWidth: imgWidth, imgHeight: imgHeight, barWidth: barWidth, barHeight: barHeight }
}

// 通过url下载
export function DownLoadFileByUrl(url) {
  window.location.href = `${process.env.BASE_API}${url}`
}

export function DownLoadFileByFileReader(res, name) {
  // 采用FileReader读取二进制流，下载读取后的结果
  let file = res.data
  let blob = new Blob([file])
  let reader = new FileReader()
  reader.readAsDataURL(blob)
  reader.onload = function (e) {
    let a = document.createElement('a')
    a.download = name // decodeURIComponent(name) //解码
    a.href = e.target.result
    document.body.appendChild(a)
    a.click()
    document.body.removeChild(a)
  }
}

export function DownLoadFileByCreateObjectURL(res, name) {
  // 通过URL.createObjectURL读取二进制流并返回一个对象URL
  let file = res.data
  let url = window.URL.createObjectURL(new Blob([file]))
  let link = document.createElement('a')
  link.style.display = 'none'
  link.href = url
  // 获取文件名fileName
  let fileName = res.headers['content-disposition'].split('=')
  fileName = fileName[fileName.length - 1]
  fileName = fileName.replace(/"/g, '')

  link.download = name || fileName
  document.body.appendChild(link)
  link.click()
  document.body.removeChild(link)
  window.URL.revokeObjectURL(url) //释放掉blob对象
}

// 导出页面为PDF格式
// 前端使用html2canvas和jspdf插件实现 HTML页面转PDF下载
/**
 *@param {Object} props title:文件名,contentId:html容器{id | class}
 */
export function htmlToPdf(props) {
  var title = props.title
  html2Canvas(document.querySelector(props.contentId), {
    allowTaint: true,
    useCORS: true
  }).then(function (canvas) {
    let contentWidth = canvas.width
    let contentHeight = canvas.height
    let pageHeight = (contentWidth / 592.28) * 841.89
    let leftHeight = contentHeight
    let position = 0
    let imgWidth = 595.28
    let imgHeight = (592.28 / contentWidth) * contentHeight
    let pageData = canvas.toDataURL('image/jpeg', 1.0)
    let PDF = new JsPDF('', 'pt', 'a4')
    if (leftHeight < pageHeight) {
      PDF.addImage(pageData, 'JPEG', 0, 0, imgWidth, imgHeight)
    } else {
      while (leftHeight > 0) {
        PDF.addImage(pageData, 'JPEG', 0, position, imgWidth, imgHeight)
        leftHeight -= pageHeight
        position -= 841.89
        if (leftHeight > 0) {
          PDF.addPage()
        }
      }
    }
    PDF.save(title + '.pdf')
  })
}



