<!--
 * author Demon3443002624@qq.com
 * description 自动滚动列表组件，注意参看 prop 定义！样式在父组件中重载即可
 * @version 2022-7-7 16:40:52 鼠标放在列表上 prop 刷新之后 timer 无法清空的情况 设置 flag 鼠标在列表上不开始滚动
 -->
<template>
  <div class="scroll-table">
    <ul class="scroll-table-head">
      <li>
        <div
          v-for="item in column"
          :key="item.key"
          :style="{ flex: item.width, display: item.width == 0 ? 'none' : '' }"
        >
          {{ item.label }}
        </div>
        <slot name="operation"></slot>
      </li>
    </ul>
    <div class="scroll-table-body" ref="scrollTable">
      <ul ref="scrollTableInner" @mouseleave="_begin" @mouseenter="_stop" @click="diagnoseHandler">
        <div>
          <li v-for="(item, idx) in tableData" :key="idx" :style="{ height: rowHeight + 'px' }">
            <div
              v-for="(item2, name) in item"
              :key="item.key + '-' + name"
              :style="{
                flex: columnObj[name].width,
                display: columnObj[name].width == 0 ? 'none' : '',
                padding: columnObj[name].width == 0 ? '0px' : '0 5px',
                cursor: title ? 'pointer' : ''
              }"
              :title="title || ''"
              :projectId="item2"
            >
              {{ item2 }}
            </div>
            <slot name="operationItem" :item="item" :idx="idx"></slot>
          </li>
        </div>
        <div v-if="!!tableData && tableData.length > 5">
          <li v-for="(item, idx) in tableData" :key="idx" :style="{ height: rowHeight + 'px' }">
            <div
              v-for="(item2, name) in item"
              :key="item.key + '-' + name"
              :style="{
                flex: columnObj[name].width,
                display: columnObj[name].width == 0 ? 'none' : '',
                padding: columnObj[name].width == 0 ? '0px' : '0 5px',
                cursor: title ? 'pointer' : ''
              }"
              :title="title || ''"
            >
              {{ item2 }}
            </div>
            <slot name="operationItem" :item="item" :idx="idx"></slot>
          </li>
        </div>
        <!-- <slot
        name="tableBody"
        :tableData="tableData"
        ></slot> -->
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: `ScrollTable`,
  data() {
    return {
      // 定时器
      timer: null,
      // 记录滚动位置
      top: 0,
      maxTop: 0,
      // 滚动标志，防抖
      flag: 0,
      mouseOnTable: false
    }
  },
  props: {
    // 滚动方案 1,2,3
    plan: {
      type: Number,
      default: 3
    },
    // 可停止标志
    canStopFlag: {
      type: Boolean,
      default: true
    },
    // 滚动速度（越小越快）
    speed: {
      type: Number,
      default: 50
    },
    // 表头 interface column : { prop , label , width , key }[], prop 为数据源的唯一标志
    column: {
      type: Array,
      default: (_) => {
        return []
      }
    },
    // 表格数据
    tableData: {
      typeof: Array,
      default: []
    },
    // 每行的高度，默认50px
    rowHeight: {
      type: Number,
      default: 50
    },
    // 表格hover
    title: {
      type: String,
      default: ''
    }
  },
  watch: {
    timer(nv) {}
  },
  mounted() {
    // window.onresize = this._resize
    this.$nextTick(function () {
      this.beginScroll()
    })
  },
  beforeUpdate() {
    // 必须清空，避免越跑越快
    this._clearInterval()
  },
  updated() {
    this.beginScroll()
  },
  destroyed() {
    this._clearInterval()
  },
  computed: {
    columnObj: function () {
      const columnObj = {}
      this.column.forEach((element) => {
        columnObj[element.prop] = element
      })
      return columnObj
    }
  },
  methods: {
    // _resize (e) {
    //   this.stopScroll()
    //   this.beginScroll()
    // },
    _begin() {
      this.mouseOnTable = false
      if (this.canStopFlag) this.beginScroll()
    },
    _stop() {
      this.mouseOnTable = true
      if (this.canStopFlag) this.stopScroll()
    },
    beginScroll() {
      if (this.mouseOnTable) return
      const st = this.$refs.scrollTable
      const sti = this.$refs.scrollTableInner
      const _html = sti.innerHTML
      const sH = sti.scrollHeight
      const cH = st.clientHeight

      if (sH > cH) {
        if (this.flag === 0) {
          // [x] 此处HTML并非VUE模板可编译的HTML，写在这会使得VUE更新视图失败
          // sti.innerHTML = _html + _html
          this.flag = 1
          this.maxTop = -sH / 2
        }
        const maxTop = this.maxTop
        clearInterval(this.timer)
        switch (this.plan) {
          case 1:
            // ** 滚动列表方案一：逐像素滚动，缩放后会卡顿
            this.timer = setInterval((_) => {
              if (this.top > maxTop) {
                sti.style.top = this.top + 'px'
                // st.scrollTop = JSON.stringify(-this.top * 0.852083)
                // sti.style.transform = `translateY(${this.top}px) scaleZ(0.852083)`
                this.top--
              } else {
                this.top = 0
              }
            }, this.speed)
            break
          case 2:
            // ** 滚动列表方案二：逐行滚动，不会卡顿，但是用户体验不友好
            this.timer = setInterval((_) => {
              if (this.top > maxTop) {
                sti.style.top = this.top + 'px'
                // st.scrollTop = JSON.stringify(-this.top * 0.852083)
                // sti.style.transform = `translateY(${this.top}px) scaleZ(0.852083)`
                this.top -= this.rowHeight
              } else {
                this.top = 0
              }
            }, 1500)
            break
          default:
            // ** 滚动列表方案三：逐行滚动，不是滚动，轮流将第一行高度强制设为0，添加动画
            this.timer = setInterval((_) => {
              const child = sti.firstChild
              const childClone = child.cloneNode(true)
              child.style.transition = 'height .4s,color .3s'
              child.style.height = 0
              child.style.color = 'transparent'
              function _change() {
                sti.lastChild.after(childClone)
                child.remove()
              }
              child.addEventListener('transitionend', _change)
            }, 2600)
            break
        }
      }
    },
    stopScroll() {
      this._clearInterval()
    },
    // 监听一下clearInterval
    _clearInterval() {
      clearInterval(this.timer)
    },
    // 滑动首行到末尾
    exchangeEl(list, el1, el2) {
      // var newNode = document.createElement('div')
      el2.after(el1)
      // list.insertAfter(newNode, el1)
      // list.insertAfter(el1, el2)
      // list.removeChild(newNode)
    },
    // 防抖
    debounce(func, wait, immediate) {
      let timer
      return function () {
        let context = this
        let args = arguments

        if (timer) clearTimeout(timer)
        if (immediate) {
          var callNow = !timer
          timer = setTimeout(() => {
            timer = null
          }, wait)
          if (callNow) func.apply(context, args)
        } else {
          timer = setTimeout(function () {
            func.apply(context, args)
          }, wait)
        }
      }
    },
    diagnoseHandler(e) {
      this.$emit('diagnoseHandler', e.target)
    }
  }
}
</script>

<style lang='less' scoped>
// 每个单元格的样式
.center {
  display: flex;
  padding: 0 5px;
  align-items: center;
  word-break: break-all;
  justify-content: start;
}
.scroll-table {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  &-head {
    height: 50px;
    & > li {
      display: flex;
      height: 100%;
      & > div {
        .center();
        font-size: 1.1rem;
        font-weight: bold;
      }
    }
  }
  &-body {
    height: calc(100% - 50px);
    flex: 1;
    overflow: hidden;
    position: relative;
    & > ul {
      position: absolute;
      width: 100%;
      // height: 100%;
      & > div {
        height: 100%;
        & > li {
          display: flex;
          & > div {
            .center();
            height: 20px;
            overflow: hidden;
            display: -webkit-box !important; /*弹性伸缩盒子*/
            -webkit-box-orient: vertical; /*子元素垂直排列*/
            -webkit-line-clamp: 1; /*可以显示的行数，超出部分用...表示*/
            text-overflow: ellipsis; /*（多行文本的情况下，用省略号“…”隐藏溢出范围的文本)*/
          }
        }
      }
    }
  }
}
</style>



