<!--
 * @Autor: linxu
 * @Date: 2022-03-17 08:53:36
 * @LastEditors: linxu
 * @LastEditTime: 2022-03-29 15:34:15
 * @Description: 项目记录表
-->
<template>
  <fm-generate-sheet
    v-if="generateSheetVisible"
    ref="generateSheet"
    class="record-sheet"
    @form-change="formChange"
    :data="{ list }"
    :value="formValue"
    :edit="edit"
  ></fm-generate-sheet>
</template>

<script>
import _ from 'lodash'

import {
  formList_01,
  formList_02,
  formList_03,
  formList_04,
  formList_05,
  formList_06,
  formList_07,
  formList_08,
  formList_09,
  formList_10,
  formList_11,
  formList_12,
  formList_13
} from './form.js'
const formLists = [
  { progressId: 1, name: '现场勘探记录表', list: formList_01(this) },
  { progressId: 2, name: '机组备机清单表', list: formList_02(this) },
  { progressId: 3, name: '机组运输记录表', list: formList_03(this) },
  { progressId: 4, name: '机组吊装记录表', list: formList_04(this) },
  { progressId: 5, name: '安装施工表', list: formList_05(this) },
  { progressId: 6, name: '安装调试记录表', list: formList_06(this) },
  { progressId: 7, name: '用户交接确认单', list: formList_07(this) },
  { progressId: 8, name: '项目完工验收单', list: formList_08(this) },
  { progressId: 9, name: '机组吊装记录表', list: formList_09(this) },
  { progressId: 10, name: '机组运输记录表', list: formList_10(this) },

  { progressId: 11, name: '项目完工结算（内）', list: formList_11(this) },
  { progressId: 12, name: '项目完工结算（外）', list: formList_12(this) },
  { progressId: 13, name: '客户满意度调查表', list: formList_13(this) }
]
export default {
  name: 'FieldExplorationRecord',

  props: ['value', 'edit'],
  data() {
    return {
      formLists,
      list: null,
      formValue: {},
      generateSheetVisible: false
    }
  },
  watch: {
    value: {
      deep: true,
      handler(val) {
        // console.log('watch------value', val)
        this.getFormValueList(val)
      }
    }
  },
  mounted() {
    console.log('mounted', this.value, this.edit)
  },
  methods: {
    /**
     * 模板数据 事件ID（ProgressID）匹配对应 表单数据
     * 模板数据 处理 成 单层表单数据
     */
    getFormValueList(val) {
      // 表单匹配
      this.formLists.forEach((item) => {
        if (item.progressId == val.ProgressId) {
          this.list = item.list
        }
      })
      // 数据处理
      const form = {}
      // 项目数据
      if (val.ProjectDetail) {
        for (let prop in val.ProjectDetail) {
          form[`key_project_${prop}`] = val.ProjectDetail[prop]
        }
      }
      // 机组数据
      if (val.GensetList) {
        form[`key_genset_list`] = val.GensetList
      }
      //普通数据
      if (val.StencilList) {
        val.StencilList.forEach((item) => {
          form[`key_${item.KeyId}`] = item.KeyValue || ''
        })
      }
      // 表格数据
      if (val.ItemList) {
        form[`key_table`] = val.ItemList
      }

      // 图片数据
      if (val.FileUrlArrays && val.FileUrlArrays.length > 0) {
        form[`key_image`] = val.FileUrlArrays.map((item) => {
          return {
            url: item
          }
        })
      }

      this.formValue = _.cloneDeep(form)
      this.generateSheetVisible = true
      console.log('formValue', this.formValue)
    },
    formChange(val) {
      // console.log('formChange', val)
    }
  }
}
</script>

<style lang="less" scoped>
.record-sheet {
  // min-height: 400px;
  border: 1px solid #e4e4e4;
  border-right: 0;
  border-bottom: 0;
}
</style>



