<!--
 * @Author: linxu
 * @Date: 2021-11-17 10:03:43
 * @LastEditors: linxu
 * @LastEditTime: 2022-04-12 14:34:28
 * @Description: 省市县 三级联动选择组件
-->

<template>
  <el-cascader
    ref="refHandle"
    v-model="selectedOptions"
    :placeholder="placeholder"
    clearable
    :options="options"
    :props="optionProps"
    @change="positionChange"
  >
  </el-cascader>
</template>

<script>
import positionOptions from '@/../static/js/distrscts'

export default {
  name: 'position-cascader',
  props: {
    placeholder: {
      type: String,
      default: '请选择省市区'
    },
    defaultPosition: {
      type: String,
      default: null
    }
  },
  data() {
    return {
      optionProps: {
        value: 'value',
        label: 'label',
        children: 'children', // 子元素字段名
        emitPath: true, // 设置true 返回由该节点所在的各级菜单的值所组成的数组，若设置 false，则只返回该节点的值
        checkStrictly: true // 父子节点不互相关联
        // expandTrigger: 'hover' // 设置鼠标悬浮展开，如果点击展开的话，会出现点击父节点后选择器直接关闭，不能选择子节点了
      },
      options: positionOptions || [],
      selectedOptions: [],
      crewPosition: '', // 机组位置

      cityArr: [], // 城市列表
      areaArr: [], // 区县列表
      province: '', // 省
      city: '', // 市
      area: '' // 区县,
    }
  },
  computed: {
    positionList() {
      const positionList = []
      this.options.map((item, index) => {
        const cityArr = item.children // 城市列表
        const province = item.label
        const provinceValue = item.value
        positionList.push({
          label: `${province}`,
          value: [provinceValue]
        })
        // 获取市名
        if (cityArr) {
          cityArr.map((item, index) => {
            const areaArr = item.children // 区县列表
            const city = item.label === '市辖区' || item.label === '县' ? '' : item.label
            const cityValue = item.value
            positionList.push({
              label: `${province}${city}`,
              value: [provinceValue, cityValue]
            })
            // 获取区县名
            if (areaArr) {
              areaArr.map((item, index) => {
                const area = item.label
                const areaValue = item.value
                positionList.push({
                  label: `${province}${city}${area}`,
                  value: [provinceValue, cityValue, areaValue]
                })
              })
            }
          })
        }
      })
      return positionList
    }
  },
  methods: {
    positionChange(value) {
      
      let self = this
      self.province = ''
      self.city = ''
      self.area = ''
      // 获取省名
      self.options.map((item, index) => {
        if (item.value === value[0]) {
          self.cityArr = item.children // 存储城市列表
          self.province = item.label
        }
      })
      // 获取市名
      self.cityArr &&
        self.cityArr.map((item, index) => {
          if (item.value === value[1]) {
            self.areaArr = item.children // 存储区县列表
            self.city = item.label === '市辖区' || item.label === '县' ? '' : item.label
          }
        })
      // 获取区县名
      self.areaArr &&
        self.areaArr.map((item, index) => {
          if (item.value === value[2]) {
            self.area = item.label
          }
        })
      // self.selectedOptions = value
      this.$set(self, 'selectedOptions', value)
      self.crewPosition = self.province + self.city + self.area
      
      this.$emit('positionChange', {
        selectedOptions: self.selectedOptions,
        crewPosition: self.crewPosition
      })
    },
    setDefaultSelected(value) {
      let filterItems = this.positionList.filter((item) => item.label === value)
      let selectedOptions = filterItems.length > 0 ? filterItems[0].value : []
      this.$set(this, 'selectedOptions', selectedOptions)
      
    }
  },
  watch: {
    selectedOptions(newVal, oldVal) {
      // 
      if (this.$refs.refHandle) {
        this.$refs.refHandle.dropDownVisible = false // 监听值发生变化就关闭它
      }
    },
    defaultPosition(newVal, oldVal) {
      
      this.setDefaultSelected(newVal)
    }
  }
}
</script>

<style lang="less">
.el-cascader {
  line-height: 32px;
}
