<!--
 * @Description:机组监控页面维保卡片图标组件
 * @Author:Demon3443002624@qq.com
 * @Date:2022-2-22 16:54:06
 * @LastEditors: Demon3443002624@qq.com
 * @LastEditTime: 2022-2-22 16:54:14 新增页面
-->
<template>
  <div class="keep-icon">
    <div>{{ title }}</div>
    <div>
      <svg-icon :icon-class="iconName"></svg-icon>
    </div>
    <div>
      <slot name="footer">{{ footer }}</slot>
    </div>
  </div>
</template>

<script>
export default {
  name: 'KeepIcon',
  props: {
    title: String,
    iconName: {
      type: String,
      default: 'crew_monitor_abandoned'
    },
    footer: String
  }
}
</script>

<style lang="less" scoped>
.keep-icon {
  width: 100%;
  height: 100%;
  > div {
    width: 100%;
    display: flex;
    justify-content: center;
    margin: 5px 0;
  }
  > div:first-child{
      margin:0;
  }
}
</style>



