<template>
  <div class="curve-report-box">
    <div class="operate-box">
      <el-button type="primary" icon="el-icon-download" @click="downloadFile">下载</el-button>
      <div class="data-source">数据来源：<span>机组信息获取</span><span>手动输入</span></div>
    </div>
    <!-- 描述字段 -->
    <div id="descriptions-box" class="descriptions-box">
      <p class="title">{{ title }}</p>
      <el-descriptions class="margin-top" title="" :column="5" :size="size" border>
        <template v-for="(item, index) in descriptionsItems">
          <el-descriptions-item
            v-if="item.children && item.children.length > 0"
            contentClassName="nopadding-box"
            :key="index"
            :span="item['size']"
          >
            <template slot="label">{{ item['label'] }}</template>
            <p style="height: 22px">{{ dataSource[item['key']] }}</p>
            <el-descriptions
              title=""
              class="sub-descriptions"
              labelClassName="descriptions-label"
              :column="4"
              border
            >
              <template v-for="(sItem, sIndex) in item.children">
                <el-descriptions-item :key="sIndex" :span="sItem['size']">
                  <template slot="label">{{ sItem['label'] }}</template>
                  <div class="description-content-box">
                    <span>{{ dataSource[sItem['key']] }}</span>
                    <span v-if="sItem['unit']">{{ sItem['unit'] }}</span>
                  </div>
                </el-descriptions-item>
              </template>
            </el-descriptions>
          </el-descriptions-item>
          <el-descriptions-item
            v-else
            labelClassName="descriptions-label"
            :key="index"
            :span="item['size']"
          >
            <template slot="label">{{ item['label'] }}</template>
            <div class="description-content-box">
              <!-- <el-input v-model="dataSource[item['key']]"
                        placeholder=""></el-input> -->
              <span>{{ dataSource[item['key']] }}</span>
              <span v-if="item['unit']">{{ item['unit'] }}</span>
            </div>
          </el-descriptions-item>
        </template>
      </el-descriptions>
      <p class="curve-separate">组合曲线</p>
      <!-- 曲线 -->
      <div class="curve-box">
        <el-row class="data-container" type="flex" justify="start">
          <el-col>
            <span>海拔高度：</span>
            <em>{{ dataSource['海拔'] ? `${dataSource['海拔']} m` : '-' }}</em>
          </el-col>
          <el-col v-if="curveType == '1' || curveType == '2'"
            ><span>水温：</span>
            <em>
              {{ dataSource['水温'] ? `${dataSource['水温'].split(',')[0] / 10} ℃` : '-' }}
            </em>
          </el-col>
          <el-col v-if="curveType == '2'">
            <span>燃油位：</span>
            <em>
              {{ dataSource['燃油位'] ? `${dataSource['燃油位'].split(',')[0] / 10} %` : '-' }}
            </em>
          </el-col>
        </el-row>
        <div class="curve-container">
          <span class="type-label" v-if="curveType == '1' || curveType == '3'">发动机</span>
          <el-checkbox-group v-if="curveType == '1' || curveType == '3'" v-model="engineCheckList">
            <template v-for="(item, index) in engineDataItemsList">
              <el-checkbox :key="index" :label="item"></el-checkbox>
            </template>
          </el-checkbox-group>
          <!-- 发动机曲线 -->
          <div style="height: 416px">
            <BaseEcharts
              v-if="engineFlag"
              :containerId="engineContainerId"
              :grid="engineGrid"
              :legendData="engineLegendData"
              :xAxisData="engineXAxisData"
              :yAxisData="engineYAxisData"
              :seriesData="engineSeriesData"
              :toolboxCfg="toolboxCfg"
            />
          </div>
        </div>
        <div
          class="curve-container generator-curve-container"
          v-if="curveType == '1' || curveType == '3'"
        >
          <span class="type-label">发电机</span>
          <el-checkbox-group v-model="generatorCheckList">
            <template v-for="(item, index) in generatorDataItemsList">
              <el-checkbox :key="index" :label="item"></el-checkbox>
            </template>
          </el-checkbox-group>
          <!-- 发电机曲线 -->
          <div style="height: 416px">
            <BaseEcharts
              v-if="generatorFlag"
              :containerId="generatorContainerId"
              :grid="generatorGrid"
              :legendData="generatorLegendData"
              :xAxisData="generatorXAxisData"
              :yAxisData="generatorYAxisData"
              :seriesData="generatorSeriesData"
              :toolboxCfg="toolboxCfg"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
