  },
  {
    label: '测试结果',
    key: '',
    unit: '',
    size: '5',
    children: [
      {
        label: '测试人员',
        key: '',
        unit: ''
      },
      {
        label: '检验人员',
        key: '',
        unit: ''
      },
      {
        label: '测试日期',
        key: '测试日期',
        unit: ''
      },
      {
        label: '测试时间',
        key: '测试时间',
        unit: ''
      }
    ]
  }
]
// 发动机 engine
const engineEchartsItems = [
  {
    label: '转速',
    type: 'line',
    yIndex: 0,
    data: []
  },
  {
    label: '油压',
    type: 'line',
    yIndex: 2,
    data: []
  },
  {
    label: '水温',
    type: 'line',
    yIndex: 0,
    data: [],
    limit: ['1', '3']
  },
  {
