    limit: ['2']
  }
]

// 发电机 generator
const generatorEchartsItems = [
  {
    label: '转速',
    type: 'line',
    yIndex: 0,
    data: []
  },
  {
    label: '发电UA',
    type: 'line',
    yIndex: 1,
    data: []
  },
  {
    label: '发电UB',
    type: 'line',
    yIndex: 1,
    data: []
  },
  {
