    type: 'line',
    yIndex: 1,
    data: []
  },
  {
    label: '发电UAB',
    type: 'line',
    yIndex: 1,
    data: []
  },
  {
    label: '发电UBC',
    type: 'line',
    yIndex: 1,
    data: []
  },
  {
    label: '发电UCA',
    type: 'line',
