    data: []
  },
  {
    label: '电流IA',
    type: 'line',
    yIndex: 2,
    data: []
  },
  {
    label: '电流IB',
    type: 'line',
    yIndex: 2,
    data: []
  },
  {
    label: '电流IC',
    type: 'line',
    yIndex: 2,
    data: []
  },
  {
    label: '有功功率',
    type: 'line',
    yIndex: 3,
    data: []
  },
  {
    label: '无功功率',
    type: 'line',
    yIndex: 4,
    data: []
  },
  {
    label: '发电频率',
    type: 'line',
    yIndex: 1,
    data: []
  },
  {
    label: '开关量输入',
    type: 'line',
    yIndex: 0,
    data: [],
    limit: ['3'],
    children: [
      '开关量输入1',
      '开关量输入2',
      '开关量输入3',
      '开关量输入4',
      '开关量输入5',
      '开关量输入6',
      '开关量输入7'
    ]
  },
  {
    label: '开关量输出',
    type: 'line',
    yIndex: 0,
    data: [],
    limit: ['3'],
    children: [
      '开关量输出1',
      '开关量输出2',
      '开关量输出3',
      '开关量输出4',
      '开关量输出5',
      '开关量输出6',
      '开关量输出7',
      '开关量输出8',
      '开关量输出9'
    ]
  },
  {
    label: '视在功率',
    type: 'line',
    yIndex: 5,
    data: []
  }
]
const generatorYAxisItems = [
  {
    label: '转速',
    unit: ''
  },
  {
    label: '电压',
    unit: ''
  },
  {
    label: '电流',
    unit: ''
  },
  {
    label: '有功功率',
    unit: ''
  },
  {
    label: '无功功率',
    unit: ''
  },
  {
    label: '视在功率',
    unit: ''
  }
]
export default {
  name: 'CurveReport',
  props: {
    curveType: {
      // 1--一键报修记录 2--启动曲线记录 3--报警曲线记录
      type: String,
      default: null
    }
  },
  components: {
    BaseEcharts
  },
  data() {
    return {
      recordInfo: JSON.parse(this.$route.query.record),
      size: '',
      descriptionsItems,
      dataSource: {},

      toolboxCfg: {
        dataViewIsShow: false,
        magicTypeIsShow: false,
        restoreIsShow: false,
        saveAsImageIsShow: true
      },
      // 发动机曲线
      engineDataItemsList: [],
      engineCheckList: [],
      engineYAxisItems,
      engineYAxisShowItems: [],

      engineFlag: false, // 更新 BaseEcharts 组件
      engineContainerId: 'engine-report-echart',
      engineGrid: {
        x: 50,
        y: 24,
        x2: 250,
        y2: 24,
        top: '20%'
      },
      engineLegendData: [],
      engineXAxisData: [],
      engineYAxisData: [],
      engineSeriesData: [],

      // 发电机曲线
      generatorDataItemsList: [],
      generatorCheckList: [],
      generatorYAxisItems,

      generatorFlag: false, // 更新 BaseEcharts 组件
      generatorContainerId: 'generator-report-echart',
      generatorGrid: {
        x: 50,
        y: 24,
        x2: 250,
        y2: 24,
        top: '20%'
      },
      color: ['#5470c6', '#91cc75', '#fac858', '#ee6666', '#73c0de', '#3ba272', '#fc8452', '#9a60b4', '#ea7ccc'],
      generatorLegendData: [],
      generatorXAxisData: [],
      generatorYAxisData: [],
      generatorSeriesData: []
    }
  },
  computed: {
    title() {
      switch (this.curveType) {
        case '1':
          return '发电机组一键组合曲线'
        case '2':
          return '发电机组启动记录'
        case '3':
          return '发电机组报警记录'
        default:
          return ''
      }
    }
  },
  watch: {
    engineCheckList: {
      handler: function (newVal, oldVal) {
        console.log('watch----engineCheckList', newVal, oldVal)
        if (this.engineFlag) {
          this.engineFlag = false
          this.getCurveData()
        }
      },
      // 深度观察监听
      deep: true
    },
    generatorCheckList: {
      handler: function (newVal, oldVal) {
        console.log('watch----generatorCheckList', newVal, oldVal)
        if (this.generatorFlag) {
          this.generatorFlag = false
          this.getCurveData()
        }
      },
      // 深度观察监听
      deep: true
    }
  },
  mounted() {
    this.pageInit()
    this.engineInit(true) // 初始化
    this.generatorInit(true) // 初始化
  },
  methods: {
    async pageInit() {
      const _this = this

      // 设置复选框组选项
      let engineDataItemsList = []
      engineEchartsItems.forEach((item) => {
        if (!item.limit || (item.limit && item.limit.includes(this.curveType))) {
          engineDataItemsList.push(item.label)
        }
      })
      this.engineDataItemsList = engineDataItemsList

      let generatorDataItemsList = []
      generatorEchartsItems.forEach((item) => {
        if (!item.limit || (item.limit && item.limit.includes(this.curveType))) {
          generatorDataItemsList.push(item.label)
        }
      })
      this.generatorDataItemsList = generatorDataItemsList

      // 设置默认选中值
      let list1 = null,
        list2 = null
      if (this.curveType === '3') {
        // 报警曲线记录默认选中项查询
        const params = { AlarmName: this.recordInfo.Trigger }
        const list = await this.$api.equipment.GetAlarmProjects(params).then((res) => {
          if (res.Code === 1) {
            console.log(res.Data)
            const nameArr = res.Data.ProjectName.map((item) => {
              return item.ProjectName
            })
            return nameArr
          }
        })
        // 取交集
        list1 = list.filter(function (val) {
          return engineDataItemsList.indexOf(val) > -1
        })
        list2 = list.filter(function (val) {
          return generatorDataItemsList.indexOf(val) > -1
        })
        // console.log(list, list1, list2)
      }
      this.engineCheckList = (function () {
        switch (_this.curveType) {
          case '1':
            return ['转速', '油压', '电瓶电压1']
          case '2':
            return [
              '转速',
              '油压',
              '电瓶电压1',
              '电瓶启动电流',
              '齿条位置',
              '电瓶电压2',
              '浮充电流'
            ]
          case '3':
            return list1 || ['转速', '油压', '电瓶电压1', '齿条位置']
          default:
            return ''
        }
      })()
      this.generatorCheckList = (function () {
        switch (_this.curveType) {
          case '1':
            return ['转速']
          case '2':
            return []
          case '3':
            return list2 || ['转速', '发电UA']
          default:
            return ''
        }
      })()

      // 设置发动机 Y轴轴线数据
      const engineYAxisShowItems = []
      engineYAxisItems.forEach((item) => {
        if (!item.limit || (item.limit && item.limit.includes(this.curveType))) {
          engineYAxisShowItems.push(item)
        }
      })
      this.engineYAxisShowItems = engineYAxisShowItems
      this.getCurveData()
    },
    // 获取曲线详情数据
    async getCurveData() {
      // 取并集
      const _this = this
      const allList = this.engineCheckList.concat(
        this.generatorCheckList.filter(function (val) {
          return !(_this.engineCheckList.indexOf(val) > -1)
        })
      )

      const params = {
        ...this.recordInfo,
        ProjectNames: allList
      }
      // 获取歌项目数据 和 数据精度
      let data = null
      let Precisions = null
      if (this.curveType === '3') {
        let result = await this.$api.equipment.GetAlarmCurveData(params).then((res) => {
          if (res.Code === 1) {
            return res.Data
          }
        })
        data = result.Data
        Precisions = result.Precisions
      } else {
        data = await this.$api.equipment.GetGenRecordCurves(params).then((res) => {
          if (res.Code === 1) {
            return res.Data
          }
        })
        // 获取精度
        if (this.curveType === '1') {
          Precisions = await this.$api.equipment
            .GetButtonRepairProjects({
              ...this.recordInfo
            })
            .then((res) => {
              if (res.Code === 1) {
                return res.Data.Precisions
              }
            })
        } else if (this.curveType === '2') {
          Precisions = await this.$api.equipment
            .GetTestReportProjects({
              ...this.recordInfo
            })
            .then((res) => {
              if (res.Code === 1) {
                return res.Data.Precisions
              }
            })
        }
      }
      console.log(data, Precisions)

      // 数据赋值到选中项模型
      engineEchartsItems.forEach((item, index) => {
        if (this.engineCheckList.includes(item.label)) {
          console.log(item.label,data[item.label])
          const arr = data[item.label] && data[item.label].split(',') || []
          const value = arr.map((num) => {
            // 解决浮点数精度计算问题
            return accMul(num, Precisions[item.label]) // num * Precisions[item.label].toFixed(1)
          })
          item.data = value && value.slice(0, 100)
        } else {
          item.data = []
        }
      })
      this.engineInit()

      generatorEchartsItems.forEach((item, index) => {
        if (this.generatorCheckList.includes(item.label)) {
          const arr = data[item.label] && data[item.label].split(',') || []
          const value = arr.map((num) => {
            return accMul(num, Precisions[item.label]) // num * Precisions[item.label].toFixed(1)
          })
          item.data = value && value.slice(0, 100)
        } else {
          item.data = []
        }
      })
      this.generatorInit()

      // 记录表数据赋值
      this.dataSource = data
    },

    // X轴 刻度设置
    getXData() {
      let data = []
      for (let i = 0; i <= 10; i++) {
        for (let j = 0; j < 10; j++) {
          if (i !== 10) {
            let floatNum = j === 0 ? i : i + '.' + j
            data.push(floatNum)
          } else {
            data.push(i)
            break
          }
        }
      }
      return data
    },

    // 发动机模块曲线配置
    engineInit(flag) {
      this.engineXAxisData = [
        {
          type: 'category',
          axisTick: {
            alignWithLabel: true
          },
          axisLabel: {
            interval: 4
          },
          boundaryGap: false,
          data: this.getXData()
        }
      ]
      const engineLegendData = []
      const engineYAxisData = []
      const engineSeriesData = []
      engineEchartsItems.forEach((item, index) => {
        if (this.engineCheckList.includes(item.label)) {
          engineLegendData.push(item.label)
          // 设置seriesData
          const engineSeriesDataItem = {
            name: item.label,
            type: item.type,
            yAxisIndex: item.yIndex,
            data: item.data,
            showAllSymbol: false
          }
          engineSeriesData.push(engineSeriesDataItem)
        }
      })
      this.engineYAxisShowItems.forEach((item, index) => {
        // 设置yAxisData
        const engineYAxisDataItem = {
          type: 'value',
          name: item.label,
          min: 0,
          // max: 250,
          position: index === 0 ? 'left' : 'right',
          offset: index >= 2 ? (index - 1) * 50 : 0,
          axisLine: {
            show: true,
            lineStyle: {
              color: this.color[index]
            }
          },
          splitLine: {
            // show: index === 0 ? true : false,
            lineStyle: {
              color: '#6E7079',
              opacity: 0.4
            }
          },
          axisLabel: {
            formatter: `{value} ${item.unit || ''}`
          }
        }
        engineYAxisData.push(engineYAxisDataItem)
      })
      this.$set(this, 'engineLegendData', engineLegendData)
      this.$set(this, 'engineYAxisData', engineYAxisData)
      this.$set(this, 'engineSeriesData', engineSeriesData)
      this.$nextTick(() => {
        if (!flag) {
          this.engineFlag = true
        }
      })
    },

    // 发电机模块曲线配置
    generatorInit(flag) {
      this.generatorXAxisData = [
        {
          type: 'category',
          axisTick: {
            alignWithLabel: true
          },
          axisLabel: {
            interval: 4
          },
          boundaryGap: false,
          data: this.getXData()
        }
      ]

      const generatorLegendData = []
      const generatorYAxisData = []
      const generatorSeriesData = []

      generatorEchartsItems.forEach((item, index) => {
        if (this.generatorCheckList.includes(item.label)) {
          if (item.children && item.children.length > 0) {
            generatorLegendData.push(...item.children)
            item.children.forEach((cItem, cIndex) => {
              const cData = item.data.map((dItem) => {
                const dData = dItem.toString(2).split('').reverse().join('').charAt(cIndex)
                return dData ? dData * 1 : 0
              })
              const generatorSeriesDataItem = {
                name: cItem,
                type: item.type,
                yAxisIndex: item.yIndex,
                data: cData,
                showAllSymbol: false
              }
              generatorSeriesData.push(generatorSeriesDataItem)
            })
            console.log(generatorLegendData, generatorSeriesData)
          } else {
            generatorLegendData.push(item.label)
            // 设置seriesData
            const generatorSeriesDataItem = {
              name: item.label,
              type: item.type,
              yAxisIndex: item.yIndex,
              data: item.data,
              showAllSymbol: false
            }
            generatorSeriesData.push(generatorSeriesDataItem)
          }
        }
      })
      this.generatorYAxisItems.forEach((item, index) => {
        // 设置yAxisData
        const generatorYAxisDataItem = {
          type: 'value',
          name: item.label,
          min: 0,
          // max: 250,
          position: index === 0 ? 'left' : 'right',
          offset: index >= 2 ? (index - 1) * 50 : 0,
          axisLine: {
            show: true,
            lineStyle: {
              color: this.color[index]
            }
          },
          splitLine: {
            // show: index === 0 ? true : false,
            lineStyle: {
              color: '#6E7079',
              opacity: 0.4
            }
          },
          axisLabel: {
            formatter: `{value} ${item.unit || ''}`
          }
        }
        generatorYAxisData.push(generatorYAxisDataItem)
      })
      this.$set(this, 'generatorLegendData', generatorLegendData)
      this.$set(this, 'generatorYAxisData', generatorYAxisData)
      this.$set(this, 'generatorSeriesData', generatorSeriesData)
      this.$nextTick(() => {
        if (!flag) {
          this.generatorFlag = true
        }
      })
    },
    // 下载
    downloadFile() {
      const props = {
        title: this.title,
        contentId: '#descriptions-box' 
      }
      htmlToPdf(props)
    },
  }
}
</script>

<style lang="less" scoped>
.operate-box {
  margin-bottom: 16px;
  display: flex;
  justify-content: space-between;
  .data-source {
    font-weight: bold;
    span {
      display: inline-block;
      background-color: #f2f2f2;
      padding: 5px 12px;
      margin-left: 4px;
      border-radius: 5px;
      color: #373a3c;
    }
  }
}
.descriptions-box {
  border: 1px solid #ebeef5;
  > p.title {
    line-height: 40px;
    text-align: center;
    font-weight: bold;
    font-size: 22px;
  }
  /deep/.el-descriptions {
    width: calc(100% + 2px);
    margin-left: -1px;
    .el-descriptions-item__cell {
      width: 200px;
      padding: 10px 8px;
      &.el-descriptions-item__label.is-bordered-label {
        text-align: center;
      }
    }
    .descriptions-label {
      width: 180px;
    }
    .nopadding-box {
      padding: 0;
      > p {
        padding: 10px 8px;
      }
    }
  }
  .description-content-box {
    display: flex;
    align-items: center;
    /deep/.el-input {
      .el-input__inner {
        border: 0;
        padding: 0;
      }
    }
    > span {
      &:first-child {
        flex: 1;
      }
      margin-left: 2px;
    }
  }
}
.sub-descriptions {
  margin: 0 -1px -1px;
}
p.curve-separate {
  text-align: center;
  font-weight: bold;
  line-height: 40px;
  border-bottom: 1px solid #ebeef5;
}
.curve-box {
  .data-container {
    line-height: 20px;
    padding: 6px;
    .el-col {
      width: auto;
      margin-right: 20px;
      em {
        display: inline-block;
        padding: 0 12px;
        box-sizing: border-box;
        border-radius: 4px;
        background-color: #ffb800;
      }
      &:first-child em {
        background-color: #1e9fff;
      }
    }
  }
  .curve-container {
    padding: 6px;
    .type-label {
      display: inline-block;
      padding: 0 6px;
      font-size: 12px;
      text-align: center;
      border-radius: 2px;
      background-color: #393d49 !important;
      color: #fff;
    }
    .el-checkbox-group {
      padding: 16px;
      border-bottom: 1px solid #ebeef5;
      margin-bottom: 16px;
      .el-checkbox {
        width: 16.66666667%;
        margin: 0 0 6px 0;
      }
    }
    #engine-report-echart,
    #generator-report-echart {
      height: 400px;
      padding-bottom: 16px;
    }
    &.generator-curve-container {
      padding-top: 8px;
      border-top: 1px solid #ebeef5;
    }
  }
}

.theme-dark .descriptions-box{
  background-color: #102447;
}
</style>



