<!--
 * @Autor: Demon3443002624@qq.com
 * @Date: 20220311 周五
 * @LastEditors: linxu
 * @LastEditTime: 2022-09-22 11:20:52
 * @Description: 维保策略（新）页面组件-可编辑列表组件
-->
<template>
  <div class="table-container">
    <el-table
      ref="myTable"
      :data="table_data.filter((data) => data.MaintenanceItemId !== -1)"
      style="width: 100%"
      @selection-change="handleSelectionChange"
    >
      <el-table-column
        v-if="radio"
        type="index"
        label="序号"
        align="center"
        width="50"
      ></el-table-column>
      <el-table-column v-if="selection" type="selection" width="55"></el-table-column>
      <el-table-column
        align="center"
        v-for="(item, index, key) in table_columns"
        :item="item"
        :key="key"
        :index="index"
        :label="item.label"
        :min-width="item.width"
      >
        <template slot-scope="scope">
          <el-input
            v-if="is_edit && item.prop === 'ItemContent'"
            size="small"
            type="textarea"
            :rows="1"
            v-model="scope.row[item.prop]"
            :placeholder="'请输入' + item.label"
          ></el-input>
          <el-input-number
            v-if="is_edit && item.prop !== 'ItemContent'"
            v-model="scope.row[item.prop]"
            :min="0"
            controls-position="right"
            :placeholder="'请输入' + item.label"
          ></el-input-number>
          <span v-if="!is_edit">{{ scope.row[item.prop] }}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" align="center" width="88" v-if="is_edit">
        <template slot-scope="scope">
          <!-- 全局控制删除 -->
          <el-button
            size="mini"
            v-if="is_delete"
            :plain="true"
            type="text"
            style="color: #ec808d"
            @click="handleDelete(scope.$index, scope.row)"
            >{{ labelText.delete }}</el-button
          >
        </template>
      </el-table-column>
    </el-table>

    <el-row type="flex" justify="center" v-if="is_edit">
      <el-button class="add-btn" size="mini" @click="handleAdd()">{{ labelText.add }}</el-button>
    </el-row>
  </div>
</template>

<script>
import _ from 'lodash'

export default {
  data() {
    return {
      new_date_json: {}, //数据结构
      multipleSelection: [], //复选框，数据
      is_edit: false, //是否可编辑
      is_delete: true, //是否可删除
      selection: false, //是否需要复选框
      radio: true, //单选变色
      space_color: false, //隔行变色
      //表格数据
      table_data: []
    }
  },
  props: {
    // 添加、编辑、删除
    labelText: {
      type: Object,
      default: (_) => {
        return {
          add: '添加项目',
          edit: '编辑',
          save: '确定',
          delete: '删除'
        }
      }
    },
    // 表头信息
    table_columns: {
      type: Array,
      default: (_) => {
        return []
      }
    },
    // 表格数据
    dataArray: {
      type: Array,
      default: (_) => {
        return []
      }
    },
    isEdit: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    space_color: function () {
      //监听数据变化
      this.$nextTick(function () {
        /////方法
        this.tableRowClassName()
      })
    },
    table_data: {
      handler: function (nv, ov) {
        this.$emit('dataChange', { data: nv })
        //监听数据变化f
        this.$nextTick(() => {
          this.tableRowClassName()
        })
      },
      deep: true
    },
    dataArray: {
      handler: function (nv, ov) {
        this.initEditAttribute()
      },
      deep: true
    },
    isEdit: {
      handler: function (nv, ov) {
        this.is_edit = nv
      },
      immediate: true
    }
  },
  created() {},
  mounted: function () {
    this.initEditAttribute()
    //确保方法在页面渲染后调用
    this.$nextTick(function () {
      /////方法
      this.tableRowClassName()
    })
  },
  methods: {
    //隔行变色
    tableRowClassName() {
      //选取DOM节点
      var trs = this.$refs.myTable.$el.getElementsByTagName('tbody')[0].getElementsByTagName('tr')
      for (var i in trs) {
        if (i % 2 == 0) {
          //当隔行变色未true时改变颜色
          if (this.space_color) {
            trs[i].style.backgroundColor = '#f0f9eb'
          } else {
            trs[i].style.backgroundColor = ''
          }
        }
      }
    },

    //多选框
    handleSelectionChange(val) {
      this.multipleSelection = val
    },
    //编辑
    handleEdit(index, row) {
      row.edit = true
    },
    //删除
    handleDelete(index, row) {
      // console.log(index, row)
      const findIndex = this.table_data.findIndex((item) => item == row)

      if (!!row.add) {
        this.table_data.splice(findIndex, 1)
        return
      }
      this.$confirm('', '', {
        message: '是否确定<span style="color:red;font-weight:bold">删除</span>该项目？',
        dangerouslyUseHTMLString: true
      })
        .then((_) => {
          const newData = {
            ...row,
            MaintenanceItemId: -1
          }
          this.table_data.splice(findIndex, 1, newData)
        })
        .catch(() => {
          return false
        })
    },
    //保存
    handleSave(index, row) {
      row.edit = false

      delete this.table_data[index].add

      this.$message({
        message: '操作成功！',
        type: 'success'
      })
    },
    handleAdd() {
      var addDataJson = {}
      for (var key in this.new_date_json) {
        if (key === 'edit') {
          delete addDataJson[key]
        } else if (key === 'add') {
          delete addDataJson[key]
        } else {
          addDataJson[key] = null
        }
      }
      addDataJson.edit = true
      addDataJson.add = true
      // console.log('handleAdd', addDataJson)
      this.table_data.push(addDataJson)
    },
    //初始化编辑属性
    initEditAttribute() {
      var self = this
      var edit = self.edit

      var dataArray = _.cloneDeep(this.dataArray)
      // console.log('dataArray----', dataArray)
      this.table_data = []
      if (dataArray && dataArray.length > 0) {
        //添加编辑事件
        for (var index in dataArray) {
          dataArray[index]['edit'] = true
          this.table_data.push(dataArray[index])
        }

        if (Object.keys(this.new_date_json).length === 0) {
          //新增时，初始化数据结构
          this.initAddDataJson(dataArray[0])
        }
      } else {
        this.new_date_json = {
          MaintenanceProgramId: null,
          MaintenanceItemId: null,
          ItemContent: '',
          DefaultHourSetting: '',
          DefaultDaySetting: '',
          edit: true,
          add: true
        }
      }
    },
    initAddDataJson(dataArray) {
      //新增时，初始化数据结构
      var dataJson = dataArray
      var newDateJson = {}
      for (var key in dataJson) {
        if (key === 'edit') {
          newDateJson[key] = 'true'
        } else {
          newDateJson[key] = ''
        }
      }
      newDateJson['add'] = true
      this.new_date_json = newDateJson
    }
  }
}
</script>
<style lang="less" scoped>
.table-container {
  max-height: calc(100% - 44px);
  // display: flex;
  // flex-direction: column;
  /deep/.el-table {
    min-height: 95px;
    max-height: calc(100% - 48px);
    overflow-y: auto;
    .cell {
      .el-input-number {
        width: 80%;
        // padding: 0 50px 0 12pz;
      }
    }
    &::before {
      height: 0;
    }
    .el-table__empty-block {
      border-bottom: 1px solid #ebeef5;
    }
  }
}
.add-btn {
  margin-top: 16px;
}
</style>



