<!--
 * @Description:进度条组件（垂直/水平）
 * @Author:Demon3443002624@qq.com
 * @Date:2022-1-21 15:10:20
 * @LastEditors:Demon3443002624@qq.com
 * @LastEditTime:2022-1-21 15:10:37
-->
<template>
  <div
    :style="{
      width: (T ? _width : _length) + 'px',
      height: (T ? _length : _width) + 'px'
    }"
    class="progress-bar"
  >
    <div
      :style="{
        height: T ? (value * 100) / max + `%` : _width + `px`,
        width: T ? _width + `px` : (value * 100) / max + `%`,
        background: color
      }"
      class="progress-bar-inner"
    ></div>
  </div>
</template>

<script>
export default {
  name: `ProgressBar`,
  props: {
    // 进度条样式横竖，默认垂直 vertical|horizontal
    type: {
      type: String,
      default: 'vertical'
    },
    // 进度条宽度（垂直进度条的宽，水平进度条的高）
    _width: {
      type: Number,
      default: 20
    },
    // 进度条长度（垂直进度条的高，水平进度条的宽）
    _length: {
      type: Number,
      default: 120
    },
    // 最大值
    max: {
      type: Number,
      default: 120
    },
    // value
    value: {
      type: [Number, String],
      default: 0
    },
    color: {
      type: String,
      default: '#495060'
    }
  },
  computed: {
    T() {
      return this.type === 'vertical'
    }
  },
  mounted() {}
}
</script>

<style lang="less" scoped>
.progress-bar {
  display: block;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
  background-color: #f5f5f5;
  border-radius: 4px;
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);
  &-inner {
    position: absolute;
    bottom: 0px;
    width: 100%;
    height: 0%;
    font-size: 12px;
    line-height: 20px;
    color: #ffffff;
    text-align: center;
    box-shadow: inset 0 -1px 0 rgba(0, 0, 0, 0.15);
    transition: all 0.6s ease;
  }
}
@keyframes meter-bar-stripes {
  from {
    background-position: 40px 0;
  }
  to {
    background-position: 0 0;
  }
}
</style>



