<template>
  <div>
    <el-dialog
      :title="title"
      class="progress-record-detail-dialog"
      width="60%"
      :visible.sync="dialigVisible"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      :append-to-body="true"
      @close="close()"
    >
      <div class="dialog-content">
        <!-- 操作按钮 -->
        <!-- 添加 -->
        <div class="operation-box" v-if="operateType === 1">
          <div class="box-left">
            <el-button v-if="IsGenset == 'true'" type="primary" @click="handleBackList()">
              上一步
            </el-button>
          </div>
          <div class="box-right">
            <!-- <el-button type="primary" @click="downloadTemplate()">下载模板</el-button> -->
            <!-- <el-button type="primary" @click="uploadFile()">上传并解析</el-button> -->
          </div>
        </div>
        <div class="operation-box" v-else-if="operateType === 4">
          <div class="box-left">
            <!-- <el-button type="primary" @click="downloadTemplate()">下载模板</el-button> -->
            <!-- <el-button type="primary" @click="uploadFile()">上传并解析</el-button> -->
          </div>
          <div class="box-right">
            <el-button type="primary" @click="handleCancelClick()">取消</el-button>
            <el-button type="primary" @click="editSave()">保存</el-button>
          </div>
        </div>
        <div class="operation-box" v-else>
          <div class="box-left">
            <el-button type="primary" @click="handleBackList()">返回列表</el-button>
            <!-- <el-button type="primary" @click="handleAppPreview()">app预览</el-button> -->
          </div>
          <div class="box-right">
            <el-button
              v-if="operateType === 2"
              type="primary"
              :disabled="projectStatus === 3"
              @click="handleEditClick()"
            >
              编辑
            </el-button>
            <el-button type="primary" @click="downloadFile()">下载</el-button>
          </div>
        </div>
        <!-- 记录表 详情 -->
        <ProjectRecordSheet
          ref="ProjectRecordSheet"
          id="ProjectRecordSheet"
          v-model="dataList"
          :edit="isEdit"
        ></ProjectRecordSheet>
      </div>
      <div slot="footer" class="dialog-footer" v-if="operateType == 1">
        <el-button @click="close()">取 消</el-button>
        <el-button type="primary" @click="addSave()">保存</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import ProjectRecordSheet from '../../projectRecordSheet.vue'
import data from '@/views/projectManage/components/data.js'
import _ from 'lodash'
const { ProjectTypeEnum } = data
import { htmlToPdf } from '@/utils/util'

export default {
  name: 'RecordFormDialog',
  components: {
    ProjectRecordSheet
  },
  props: {
    recordParmas: {
      type: Object,
      default: null
    },
    RecordId: {
      type: Number,
      default: null
    },
    OperateType: {
      type: Number,
      default: null
    },
    GensetList: {
      type: Array,
      default: null
    }
  },
  data() {
    return {
      loading: false,
      dialigVisible: true,

      projectStatus: null,
      ProjectId: null,
      ProjectProgressId: null,
      IsGenset: null,
      IsGensetMult: null,

      // operateType 操作类型 {1：项目-添加, 2: 项目-查看, 3: 单机-查看, 4:项目-编辑}
      operateType: this.OperateType,
      // 记录表数据
      dataList: [],

      ProjectTypeEnum
    }
  },
  watch: {
    recordParmas: {
      handler: function (newVal, oldVal) {
        console.log('recordParmas', newVal)
        this.projectStatus = newVal.projectStatus // 项目状态
        this.ProjectId = newVal.ProjectId
        this.ProjectProgressId = newVal.ProjectProgressId
        this.IsGenset = newVal.IsGenset
        this.IsGensetMult = newVal.IsGensetMult
      },
      deep: true,
      immediate: true
    }
  },
  computed: {
    title() {
      let str = ''
      switch (this.operateType) {
        case 1:
          if (this.IsGenset == 'true') {
            return '第二步：完善记录内容'
          }
          return '工程进度记录新增'
          break
        case 2:
          str = '查看'
          break
        case 3:
          str = '查看'
          break
        case 4:
          str = '编辑'
          break
        default:
          str = ''
      }
      return '工程进度记录' + str
    },
    isEdit() {
      return this.operateType == 1 || this.operateType == 4 ? true : false
    }
  },
  mounted() {
    console.log(
      this.ProjectProgressId,
      this.RecordId,
      this.OperateType,
      this.GensetList,
      this.ProjectId
    )
    // console.log(this.isEdit)
    if (!this.RecordId) {
      // 添加
      this.GetProgressStencil()
    } else {
      // 查看、编辑
      this.getProgressFormData()
    }
  },
  methods: {
    // 获取模板
    GetProgressStencil() {
      const params = {
        ProgressId: this.ProjectProgressId,
        ProjectId: this.ProjectId
      }
      this.$api.project.GetGensetProjectProgressStencilProject(params).then((res) => {
        if (res.Code === 1) {
          const projectInfo = res.Data.ProjectDetail
          const data = {
            ProjectDetail: {
              ...projectInfo,
              ProjectTypeStr: projectInfo.ProjectType
                ? this.ProjectTypeEnum.find((item) => item.value == projectInfo.ProjectType).label
                : ''
            },
            ...res.Data.StencilDetail
          }
          data.StencilList = data.StencilModel
          data.ItemList = data.ItemModel || [{}]

          console.log(res, data)

          if (data.IsGenset) {
            // 绑定机组
            data.GensetList = this.GensetList
          }
          this.$set(this, 'dataList', _.cloneDeep(data))
        }
      })
    },
    // 查看 获取表单数据
    getProgressFormData() {
      const query = {
        RecordId: this.RecordId
      }
      this.$api.project.GetGensetProjectProgressRecordDetail({}, query).then((res) => {
        if (res.Code === 1) {
          console.log('bianji', res)

          const data = res.Data
          this.$set(
            data,
            'GensetList',
            data.GensetList
              ? data.GensetList.map((item) => {
                  return {
                    RecordJoinGensetId: item.RecordJoinGensetId,
                    ...item.GeneratorSet
                  }
                })
              : []
          )
          // 项目信息
          this.$set(data, 'ProjectDetail', {
            ...data.ProjectDetail,
            ProjectTypeStr: data.ProjectDetail.ProjectType
              ? this.ProjectTypeEnum.find((item) => item.value == data.ProjectDetail.ProjectType)
                  .label
              : ''
          })
          this.$nextTick(() => {
            this.$set(this, 'dataList', _.cloneDeep(data))
          })
        }
      })
    },

    close() {
      this.$emit('closeDialog')
    },

    // 新建保存
    addSave() {
      this.$refs.ProjectRecordSheet.$refs.generateSheet.getData().then((data) => {
        console.log('formdata-----------', this.dataList, data)
        let params = {
          ProjectId: this.ProjectId,
          ProgressId: this.ProjectProgressId,
          IsGenset: this.dataList.IsGenset,
          GensetList: this.GensetList
            ? this.GensetList.map((item) => {
                return {
                  GeneratorSetId: item.GeneratorSetId
                }
              })
            : [],
          FileUrl: '',
          StencilList: this.dataList.StencilModel
            ? Object.assign([], this.dataList.StencilModel)
            : null,
          ItemList: data[`key_table`] || this.dataList.ItemModel
        }
        params.StencilList &&
          params.StencilList.forEach((item) => {
            item[`KeyValue`] = data[`key_${item.KeyId}`]
          })

        if (data[`key_image`]) {
          params.FileUrl = data[`key_image`].map((item) => item.url).join(';')
        }
        console.log('params', params)

        this.$api.project.AddGensetProjectProgressRecord(params).then((res) => {
          if (res.Code === 1) {
            console.log(res)
            this.$emit('closeDialog', { refresh: true })
          }
        })
      })
    },

    // 编辑-保存
    editSave() {
      const query = {
        id: this.RecordId
      }
      this.$refs.ProjectRecordSheet.$refs.generateSheet.getData().then((data) => {
        console.log('formdata-----------', this.dataList, data)
        let params = {
          ProjectId: this.ProjectId,
          ProgressId: this.ProjectProgressId,
          IsGenset: this.dataList.IsGenset,
          IsGensetMult: this.dataList.IsGensetMult,
          GensetList: this.dataList.GensetList.map((item) => {
            return {
              GeneratorSetId: item.GeneratorSetId
            }
          }),
          FileUrl: '',
          StencilList: this.dataList.StencilList
            ? Object.assign([], this.dataList.StencilList)
            : null,
          ItemList: data[`key_table`] || this.dataList.ItemList
        }
        params.StencilList &&
          params.StencilList.forEach((item) => {
            item[`KeyValue`] = data[`key_${item.KeyId}`]
          })

        if (data[`key_image`]) {
          params.FileUrl = data[`key_image`].map((item) => item.url).join(';')
        }
        console.log('params', params)

        this.$api.project.EditGensetProjectProgressRecord(params, query).then((res) => {
          if (res.Code === 1) {
            console.log(res)
            this.$emit('closeDialog', { refresh: true })
          }
        })
      })
    },
    // 取消
    handleCancelClick() {
      this.operateType = 2
      this.getProgressFormData()
    },
    // 编辑
    handleEditClick() {
      this.operateType = 4
    },
    // 下载模板
    downloadTemplate() {},
    // 上传并解析
    uploadFile() {},
    // 返回列表
    handleBackList() {
      this.close()
    },
    // 下载
    downloadFile() {
      const props = {
        title: '页面导出PDF文件名',
        contentId: '#ProjectRecordSheet' // this.$refs.ProjectRecordSheet
      }
      htmlToPdf(props)
    },

    // app 预览
    handleAppPreview() {
      const link = this.$router.resolve({
        path: '/project/app-record-form',
        query: { RecordId: this.RecordId }
      })
      window.open(link.href, '_blank')
    }
  }
}
</script>

<style lang="less" scoped>
.progress-record-detail-dialog {
  /deep/.el-dialog .el-dialog__body {
    padding: 25px;
    .dialog-content {
      .el-input__inner,
      .el-textarea__inner {
        background: transparent !important;
        color: #fff !important;
      }
    }
  }
  .operation-box {
    margin-bottom: 16px;
    display: flex;
    .box-left,
    .box-right {
      width: 50%;
    }
    .box-right {
      text-align: right;
    }
  }
}
.theme-dark #ProjectRecordSheet {
  background-color: #102447;
}
</style>



