<!--
 * @Author: linxu
 * @Date: 2021-11-17 10:03:43
 * @LastEditors: linxu
 * @LastEditTime: 2022-08-19 16:13:15
 * @Description: 全局切换选中机组 组件
-->

<template>
  <div class="toggle-crew">
    <p class="content-title">所选机组</p>
    <div class="select-content">
      <p v-if="selectCrewInfo.ID">
        当前机组: &nbsp;&nbsp;{{ selectCrewInfo.DisplayName }}
        <span :class="`crew-status crew-status-${selectCrewInfo.Status}`">
          【{{ showByCrewStatus(selectCrewInfo.Status).text }}】
          <!-- crewStatusList[parseInt(selectCrewInfo.Status)]  -->
        </span>
      </p>
      <p v-else>当前机组: -</p>
      <div class="toggle-search-box">
        <div class="search-item">
          <span>机组编码搜索:</span>
          <el-input placeholder="请输入机组编码" v-model="crewCode" @input="crewCodeInput">
          </el-input>
        </div>
        <div class="search-item">
          <span>机组状态:</span>
          <el-select
            v-model="crewStatus"
            placeholder="请选择状态"
            @change="crewStatusChange"
            :popper-append-to-body="false"
          >
            <el-option
              v-for="item in statusList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </div>
        <div class="search-item">
          <span>机组位置筛选:</span>
          <position-cascader placeholder="请选择机组所在位置" @positionChange="positionChange" />
          <!-- <el-cascader
            v-model="selectedOptions"
            placeholder="请选择机组所在位置"
            :options="options"
            @change="positionChange"></el-cascader> -->
        </div>
        <div class="search-item">
          <span>机组列表:</span>
          <el-select
            v-model="crewId"
            placeholder="请选择机组"
            :popper-append-to-body="false"
            v-lazy-load="lazyOption"
          >
            <el-option
              v-for="item in searchData"
              :key="item.ID"
              :label="item.DisplayName"
              :value="item.ID"
            >
            </el-option>
          </el-select>
        </div>
        <el-button type="primary" class="change-btn" @click="toggleCrew()">切换机组</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import PositionCascader from '@/components/PositionCascader'
import { showByCrewStatus } from '@/utils/common'

export default {
  name: 'toggle-crew',
  inject: ['reload'], // 注入App中注册的变量
  components: {
    PositionCascader
  },
  data() {
    return {
      showByCrewStatus,
      crewStatusList: ['离线', '停机', '警告', '报警', '运行'], // 0离线 1停机 2警告 3报警 4运行
      statusList: [
        { value: 0, label: '所有' },
        { value: 2, label: '报警' },
        { value: 3, label: '警告' },
        { value: 4, label: '运行' },
        { value: 5, label: '停机' },
        { value: 6, label: '离线' }
      ], // 0 所有 2报警 3警告 4运行 5 停机 6离线
      selectCrewInfo: {}, // 已选中机组
      crewList: [], // 机组list数据
      crewCode: '', // 机组编码
      crewStatus: 0, // 机组状态
      crewPosition: '', // 机组位置
      crewId: '', // 选中机组id

      // 懒加载+分页逻辑
      searchData: [],
      curPage: 1,
      pageSize: 20,
      lazyOption: {
        loadData: this.loadData,
        distance: 20,
        scrollBody: '.el-scrollbar__wrap', // 为el-select 滚动容器的DOM元素的class选择器
        changeCurPage: this.changeCurPage,
        callback: (fn) => {
          // 这里是在组件销毁前, 移除监听事件.
          this.$once('hook:beforeDestroy', () => fn())
        }
      }
    }
  },
  computed: {
    ...mapGetters(['selectCrew'])
  },
  mounted() {
    //
    this.selectCrewInfo = this.selectCrew
    this.getCrewList()
  },
  methods: {
    ...mapActions(['toggleSelectCrew']),
    getCrewList() {
      const query = {
        // IsWeb: true,
        page: 0,
        pageSize: 999
      }
      const params = {
        model: {
          DisplayMode: 1, // 1为所有启用，0为所有机组 2为所有禁用
          IsLease: 1, // 租赁
          UserID: -1,
          CollectorToken: this.crewCode, // 标识
          StatusMode: this.crewStatus, // 0 所有 2报警 3警告 4运行 5 停机 6离线
          InstallationSite: this.crewPosition
        }
      }
      this.$api.common.getCrewList(params, query).then((res) => {
        if (res.Code == 1) {
          this.crewList = res.Data.Data
          this.loadData()
          if (
            res.Data &&
            res.Data.Data &&
            res.Data.Data.length > 0 &&
            !(this.selectCrewInfo && this.selectCrewInfo.ID)
          ) {
            this.selectCrewInfo = res.Data.Data[0]
            this.toggleSelectCrew(this.selectCrewInfo)
          }
        }
      })
    },
    loadData() {
      // console.log('loadData', this.crewList, this.lazyOption)
      this.searchData = this.crewList.slice(0, this.pageSize * this.curPage)
      // console.log(this.searchData)
    },
    changeCurPage() {
      this.curPage++
    },
    crewCodeInput(value) {
      // this.crewCode = value
      this.crewId = ''
      this.getCrewList()
    },
    crewStatusChange(value) {
      this.crewId = ''
      this.getCrewList()
    },
    positionChange(data) {
      this.crewPosition = data.crewPosition
      this.crewId = ''
      this.crewList = []
      this.getCrewList()
    },
    toggleCrew() {
      if (this.crewId) {
        const selectInfo = this.crewList.filter((item) => item.ID === this.crewId)[0]
        this.toggleSelectCrew(selectInfo)
        return
      }
      this.$message({
        type: 'error',
        message: '请选择机组！'
      })
    }
  },
  watch: {
    // 监听选中机组切换
    selectCrew(newVal, oldVal) {
      //
      this.selectCrewInfo = newVal
    }
  }
}
</script>

<style lang="less" scoped>
.crew-status-0 {
  color: #808080;
}
.crew-status-1 {
  color: #2e2d28;
}
.crew-status-2 {
  color: #ffd800;
}
.crew-status-3 {
  color: red;
}
.crew-status-4 {
  color: #5cf548;
}
.toggle-crew {
  margin-bottom: 16px;
  .select-content {
    padding: 0 16px;
    > p {
      margin-bottom: 16px;
    }
    .crew-status {
      font-weight: 700;
    }
    .toggle-search-box {
      display: flex;
      margin-bottom: 16px;
      .search-item {
        width: 25%;
        margin-right: 24px;
        display: flex;
        align-items: center;
        > span {
          white-space: nowrap; /*强制span不换行*/
          margin-right: 8px;
        }
      }
    }
    .change-btn {
      height: 32px;
      // margin-top: 4px;
    }
  }
}
</style>



