<!--
    @author Demon3443002624@qq.com
    @version 1.0 2022-1-12 09:11:42
    @description 项目筛选组件（暂仅用于工程-工程管理-项目监控功能）
 -->
<template>
  <div class="project-select">
    <el-row type="flex" class="title content-title" align="middle"> 选择项目 </el-row>
    <el-row class="value" type="flex" align="middle"> 当前项目：{{ currentProjectName }} </el-row>
    <el-row type="flex" class="items">
      <el-col :span="5">
        <label class="label">项目名称： </label>
        <el-input v-model="itemName" placeholder="请输入项目名称"></el-input>
      </el-col>
      <!-- <el-col :span="5">
        <label class="label">项目编号： </label>
        <el-input v-model="itemNum" placeholder="请输入项目编号"></el-input>
      </el-col> -->
      <el-col :span="5">
        <label class="label">项目类型： </label>
        <el-select v-model="itemType" placeholder="请选择项目类型" :popper-append-to-body="false">
          <el-option
            v-for="item in itemTypeOpt"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-col>
      <el-col :span="5">
        <label class="label">项目列表：</label>
        <el-select v-model="itemValue" placeholder="请选择项目" :popper-append-to-body="false">
          <el-option
            v-for="(item, index) in itemValueOpt"
            :key="index"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </el-col>
      <el-col :span="4">
        <el-button type="primary" @click="switchHandler" size="small">切换项目</el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'

export default {
  name: 'ProjectSelect',
  data() {
    return {
      itemName: '',
      itemNum: '',
      itemType: '',
      itemValue: '',
      itemTypeOpt: [],
      itemValueOpt: [],
      currentProjectName: ''
    }
  },
  computed: {
    ...mapGetters(['currentProject'])
  },
  watch: {
    itemName(nv) {
      this.getItemValueOpt()
    },
    itemType(nv) {
      this.getItemValueOpt()
    },
    // computed 有缓存，监听项目选择改变才更新值
    currentProject(nv) {
      this.currentProjectName = nv.ProjectName
      // 项目改变，更新页面内容
    }
  },
  methods: {
    ...mapActions(['toggleCurrentProject']),
    // 切换项目点击事件
    switchHandler() {
      this.$api.project.GetGensetProject({ id: this.itemValue }).then((value) => {
        const data = value.Data
        if (data) this.toggleCurrentProject(data)
      })
    },
    // 获取项目类型选项，可能从服务器获取
    getItemTypeOpt() {
      this.itemTypeOpt = [
        { value: 1, label: '运维' },
        { value: 2, label: '代运维' }
      ]
    },
    // 获取项目列表，从服务器拉取
    getItemValueOpt() {
      const itemValueOpt = []
      const q = { Page: 1, PageSize: 999 }
      const p = {
        ProjectName: this.itemName.toString(),
        ProjectType: this.itemType.toString(),
        Status: '',
        StartTime: '',
        EndTime: '',
        StartCreateTime: '',
        EndCreateTime: '',
        IsDue: ''
      }
      this.$api.project
        .GetGensetProjectManage(p, q)
        .then((value) => {
          const data = value && value.Data && value.Data.Data
          if (data && data.length && data.length > 0) {
            data.forEach((item) => {
              const obj = {
                value: item.ProjectId,
                label: item.ProjectName
              }
              itemValueOpt.push(obj)
            })
            this.itemValueOpt = itemValueOpt
            this.$nextTick(function () {
              const projectInfo = this.currentProject
              if (!!projectInfo.ProjectName) {
                this.currentProjectName = projectInfo.ProjectName
                this.itemValue = projectInfo.ProjectId
                // 项目改变，更新页面内容
              } else {
                this.itemValue = itemValueOpt[0].value
                this.switchHandler()
              }
            })
          }
        })
        .catch((result) => {
          this.$message.warning('获取项目列表信息失败，请检查网络！')
          console.error(result)
        })
    }
  },
  created() {},
  mounted() {
    this.getItemTypeOpt()
    this.getItemValueOpt()
  }
}
</script>

<style lang="less" scoped>
// 统一内边距
.padding {
  padding: 0 0 0 15px;
}
// label 标签显示手型
label {
  cursor: pointer;
}
.title {
  .padding();
  height: 45px;
  background: #f3f3f3;
  border: 1px solid #e4e4e4;
  margin: 0;
}
.value {
  .padding();
  height: 40px;
}
.items {
  .padding();
  > div {
    display: flex;
    margin-right: 8px;
    .label {
      display: flex;
      align-items: center;
      white-space: nowrap;
      justify-content: center;
    }
  }
  div:last-child {
    display: flex;
    align-items: center;
  }
}
</style>



