<!--
 * @Description:机组实时数据监控页面-仪表盘组件
 * @Author:Demon3443002624@qq.com
 * @Date:2022-1-21 15:10:20
 * @LastEditors: linxu
 * @LastEditTime: 2022-05-26 10:40:04
-->
<template>
  <div :id="id" class="gauge"></div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: `Gauge`,
  data() {
    return {
      chart: null
    }
  },
  props: {
    // chart容器id
    id: {
      type: String,
      default: 'gauge'
    },
    // 最大值
    max: {
      type: Number,
      default: 100
    },
    // label 的单位
    unit: {
      type: String,
      default: 'RPM'
    },
    value: {
      type: [Number, String],
      default: 0
    },
    axisLineSetting: {
      type: Array,
      default: null
    }
  },
  computed: {
    ...mapGetters(['theme'])
  },
  watch: {
    // 重新渲染图表
    value(nv, ov) {
      this.$render()
    },
    theme(nv) {
      this.$render()
    },
    max(nv) {
      this.$render()
    }
  },
  methods: {
    $render() {
      this.chart = this.chart || this.$echarts.init(document.getElementById(this.id))
      const opt = {
        series: [
          {
            radius: '100%',
            type: 'gauge',
            axisLine: {
              lineStyle: {
                width: 15,
                color: this.axisLineSetting || [
                  // [0.3, '#67e0e3'],
                  // [0.7, '#37a2da'],
                  [1, '#495060']
                ]
              }
            },
            pointer: {
              itemStyle: {
                color: 'inherit'
              },
              length: '90%'
            },
            splitNumber: 5,
            // 刻度
            axisTick: {
              show: true,
              distance: -20,
              // distance:0,
              length: 15,
              splitNumber: 10,
              lineStyle: {
                color: this.theme === 'dark' ? '#102447' : '#fff',
                width: 1
              }
            },
            min: 0,
            max: this.max,
            // 刻度添加分割线
            splitLine: {
              distance: -30,
              length: 30,
              lineStyle: {
                color: this.theme === 'dark' ? '#102447' : '#fff',
                width: 2
              }
            },
            emphasis: {
              // v5.3.0 支持，无效，使用 silent
              disable: true
            },
            // 不响应和触发鼠标事件，默认为 false，即响应和触发鼠标事件。
            silent: true,
            // 刻度标签
            axisLabel: {
              color: this.theme === 'dark' ? '#c0c4c3' : '#495060',
              distance: 20,
              fontSize: 10
            },
            detail: {
              valueAnimation: true,
              formatter: '{value} ' + this.unit,
              color: this.theme === 'dark' ? '#c0c4c3' : '#495060',
              offsetCenter: [0, 80],
              fontSize: 15
            },
            data: [
              {
                value: this.value
              }
            ]
          }
        ]
      }
      this.chart.setOption(opt)
    },
    $resize() {
      this.chart.resize()
    }
  },
  mounted() {
    this.$render()
    // 注册窗口尺寸改变监听
    window.addEventListener(`resize`, this.$resize)
  }
}
</script>

<style lang="less" scoped>
.gauge {
  width: 100%;
  height: 100%;
}
</style>



