<!--
 * @Author: linxu
 * @Date: 2022-09-13 17:01:20
 * @LastEditors: linxu
 * @LastEditTime: 2022-09-20 16:09:39
 * @Description: 引用策略模板弹窗
-->
<template>
  <el-dialog
    title="引用维保策略模板信息"
    class="select-strategy-dialog"
    width="1080px"
    :visible.sync="dialigVisible"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    :append-to-body="true"
    @close="closeDialog()"
  >
    <div class="select-strategy-content">
      <div class="left">
        <p class="title">维保策略模板</p>
        <div class="search-box">
          <div class="search-item">
            <!-- <span>维保策略名称:</span> -->
            <el-input
              placeholder="支持维保策略名称关键词搜索"
              v-model="searchForm.StrategyName"
              @change="resetCurrentParams()"
              @keyup.enter.native="resetCurrentParams()"
            >
            </el-input>
          </div>
          <div class="search-item">
            <!-- <span>发动机品牌:</span> -->
            <el-select
              v-model="searchForm.EngineBrandId"
              :popper-append-to-body="false"
              placeholder="请选择发动机品牌"
              @change="resetCurrentParams()"
              @keyup.enter.native="resetCurrentParams()"
            >
              <el-option label="全部发动机品牌" value=""></el-option>
              <el-option
                v-for="item in EngineBrandList"
                :key="item.EngineBrandId"
                :label="item.BrandName"
                :value="item.EngineBrandId"
              ></el-option>
            </el-select>
          </div>
          <div class="search-item">
            <!-- <span>策略主备分类:</span> -->
            <el-select
              v-model="searchForm.Type"
              :popper-append-to-body="false"
              placeholder="请选择策略主备分类"
              @change="resetCurrentParams()"
              @keyup.enter.native="resetCurrentParams()"
            >
              <el-option label="全部策略主备" value=""></el-option>
              <el-option
                v-for="item in StrategyTypeEnum"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </div>
          <!-- <div class="search-btn-box">
              <el-button type="text" class="reset-btn" @click="reset()">清空筛选</el-button>
              <el-button type="primary" class="query-btn" @click="queryTableData()">查 询</el-button>
            </div> -->
        </div>
        <div class="data-list">
          <ul v-if="dataTotal > 0">
            <li
              class="item-card"
              v-for="item in dataSource"
              :key="item.StrategyId"
              :class="{ active: selectId && selectId == item.StrategyId }"
              @click="handleSelect(item)"
            >
              <div class="part-left">
                <p>{{ item.StrategyName }}</p>
                <p>
                  <span>{{ item.EngineBrandName }}</span
                  ><span>{{ showType(item.Type) }}</span>
                </p>
              </div>
              <div class="part-right">
                <el-radio v-show="selectId && selectId == item.StrategyId" v-model="radio" label="1"
                  >已选择</el-radio
                >
              </div>
            </li>
          </ul>
          <p class="no-data" v-else>暂无记录！</p>
          <!-- 分页部分 -->
          <el-pagination
            v-if="dataTotal > 0"
            background
            class="pagination"
            small
            :total="dataTotal"
            :page-size="pagination.pageSize"
            :current-page="pagination.currentPage"
            @size-change="handleChangeSize"
            @current-change="handleChangePage"
            :page-sizes="pagination.pageSizes"
            :pager-count="pagination.pagerCount"
            :layout="
              pagination && pagination.layout
                ? pagination.layout
                : 'total, prev, pager, next,jumper'
            "
          />
        </div>
      </div>

      <div class="right">
        <template v-if="selectId">
          <div class="part-left">
            <p class="title">基本信息</p>
            <ul>
              <li v-for="(item, index) in list" :key="index">
                <span>{{ item.label }}:</span>
                <span>{{ selectStrategyDetail[item.key] }}</span>
              </li>
            </ul>
          </div>
          <div class="part-right">
            <p class="title">维保事项</p>
            <el-table
              ref="myTable"
              :data="selectStrategyDetail.StrategyItemList"
              style="width: 100%"
            >
              <el-table-column
                type="index"
                label="序号"
                align="center"
                width="50"
              ></el-table-column>
              <el-table-column prop="ItemContent" label="维保事项" align="center" min-width="40%">
                <!-- <template slot-scope="scope">
                  <span>{{scope.row.ItemContent}}</span>
                </template> -->
              </el-table-column>
              <el-table-column
                prop="DefaultHourSetting"
                label="运行时间(h)"
                align="center"
                min-width="30%"
              >
                <!-- <template slot-scope="scope">
                  <span>{{scope.row.DefaultHourSetting}}</span>
                </template> -->
              </el-table-column>
              <el-table-column
                prop="DefaultDaySetting"
                label="绝对时间(天)"
                align="center"
                min-width="30%"
              >
                <!-- <template slot-scope="scope">
                  <span>{{scope.row.DefaultDaySetting}}</span>
                </template> -->
              </el-table-column>
            </el-table>
          </div>
        </template>
        <div class="no-data" v-else>
          <p>提示：选择左侧的维保策略模板后即可查看模板详情</p>
        </div>
      </div>
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button type="primary" :disabled="!selectId" @click="handleConfirmQuote()"
        >确认引用模板并替换已录入的维保策略信息</el-button
      >
    </div>
  </el-dialog>
</template>

<script>
import _data from './data'
const { StrategyTypeEnum } = _data
const list = [
  { key: 'StrategyName', label: '维保策略名称' },
  { key: 'EngineBrandName', label: '发动机品牌' },
  { key: 'EngineSeriesName', label: '发动机系列' },
  { key: 'BeatLength', label: '节拍时长' },
  { key: 'BeatCount', label: '维保节拍个数' },
  { key: 'CycleTime', label: '维保周期时长' },
  { key: 'CycleCount', label: '维保周期个数' },
  { key: 'AllTime', label: '检修周期时长' },
  { key: 'TypeStr', label: '策略主备分类' }
]

const table_columns = [
  {
    prop: 'ItemContent',
    label: '维保事项',
    width: '100'
  },
  {
    prop: 'DefaultHourSetting',
    label: '运行时间(h)',
    width: '100'
  },
  {
    prop: 'DefaultDaySetting',
    label: '绝对时间(天)',
    width: '100'
  }
]

export default {
  name: 'SelectStrategyContent',
  data() {
    return {
      dialigVisible: true,
      loading: false,
      EngineBrandList: [], // 发动机品牌
      StrategyTypeEnum,
      // 筛选条件
      searchForm: {
        StrategyName: '',
        EngineBrandId: '',
        Type: ''
      },
      dataSource: [],
      StrategyItemList: [],
      selectId: null, // 选中模板数据 Id
      selectStrategyDetail: null,
      radio: '1',
      // 分页
      dataTotal: 0,
      pagination: {
        currentPage: 1,
        pageSize: 10,
        layout: 'prev, pager, next,jumper',
        pageSizes: [10, 20, 30, 40],
        pagerCount: 5
      },

      list
    }
  },
  mounted() {
    this.getEngineBrandList()
    this.queryTableData()
  },
  methods: {
    // 查询发动机品牌
    getEngineBrandList() {
      this.$api.operation.GetMaintenanceGensetEngineBrand().then((res) => {
        if (res.Code === 1) {
          this.$set(this, 'EngineBrandList', res.Data)
        }
      })
    },
    // 查询维保策略
    queryTableData() {
      this.loading = true
      const query = {
        Page: this.pagination.currentPage,
        PageSize: this.pagination.pageSize
      }
      const params = {
        ...this.searchForm
      }
      this.$api.operation
        .GetMaintenanceGensetStrategyManage(params, query)
        .then((res) => {
          if (res.Code === 1) {
            this.dataTotal = res.Data.TotalItemCount
            let data = res.Data.Data
            this.$set(this, 'dataSource', data)
          }
          this.loading = false
        })
        .catch(() => {
          this.loading = false
        })
    },

    resetCurrentParams() {
      this.pagination.currentPage = 1
      this.queryTableData()
    },

    // 分页 当前页改变
    handleChangePage(val) {
      this.pagination.currentPage = val
      this.queryTableData()
    },
    // 分页 每页条数改变
    handleChangeSize(val) {
      this.pagination.currentPage = 1
      this.pagination.pageSize = val
      this.queryTableData()
    },
    showType(val) {
      return val ? this.StrategyTypeEnum.find((item) => item.value == val).label : '-'
    },
    // 选中
    handleSelect(row) {
      // console.log(row)
      // 查询维保策略模板详情
      this.$api.operation
        .GetMaintenanceGensetStrategyDetail({}, { StrategyId: row.StrategyId })
        .then((res) => {
          if (res.Code === 1) {
            const StrategyItemList = res.Data.StrategyItemList.map((item) => {
              return {
                ItemContent: item.ItemContent,
                DefaultHourSetting: item.DefaultHourSetting,
                DefaultDaySetting: item.DefaultDaySetting,
                MaintenanceProgramId: null,
                MaintenanceItemId: null,
                add: true
              }
            })
            let BeatLength = res.Data.BeatLength,
              BeatCount = res.Data.BeatCount,
              CycleCount = res.Data.CycleCount
            this.selectStrategyDetail = {
              ...res.Data,
              CycleTime: BeatLength * BeatCount,
              AllTime: BeatLength * BeatCount * CycleCount,
              TypeStr: this.showType(res.Data.Type),
              StrategyItemList: StrategyItemList
            }
            this.selectId = row.StrategyId
            // console.log('this.selectStrategyDetail',this.selectStrategyDetail)
          }
        })
    },
    // 确认引用
    handleConfirmQuote() {
      this.$emit('handleConfirmQuote', this.selectStrategyDetail)
      this.$emit('closeDialog')
    },
    closeDialog() {
      this.$emit('closeDialog')
    }
  }
}
</script>

<style lang="less" scoped>
@import '~@/styles/variables.less';
.select-strategy-dialog {
  .select-strategy-content {
    display: flex;
    .left {
      width: 280px;
      display: flex;
      flex-direction: column;
      border-right: 1px solid @borderColor;
      padding-right: 16px;
      p.title {
        margin-bottom: 12px;
      }
      .search-box {
        background: transparent;
        border: 0;
        padding: 0;
        margin-bottom: 10px;
        /deep/div.search-item {
          width: 50%;
          padding-right: 8px;
          &:nth-child(1) {
            width: 100%;
            padding-right: 0;
          }
          &:last-child {
            padding-right: 0;
          }
        }
      }
      .data-list {
        flex: 1;
        display: flex;
        flex-direction: column;
        ul {
          flex: 1;
          padding-bottom: 16px;
          overflow: auto;
          max-height: 200px;
          margin-bottom: 16px;
          li {
            display: flex;
            align-items: center;
            padding: 4px 8px;
            background: #7f7f7f;
            margin-bottom: 12px;
            border-radius: 4px;
            .part-left {
              flex: 1;
              p span {
                margin-right: 24px;
              }
            }
            .part-right {
              width: 72px;
              text-align: right;
            }

            &.active {
              color: @theme;
            }
          }
        }
        .no-data {
          text-align: center;
        }
      }
    }
    .right {
      flex: 1;
      height: 400px;
      max-height: 400px;
      overflow: auto;
      display: flex;
      > div {
        padding: 0 0 0 16px;
        .title {
          margin-bottom: 12px;
        }
      }
      .part-left {
        width: 260px;
        ul li {
          line-height: 32px;
        }
      }
      .part-right {
        flex: 1;
      }

      .no-data {
        flex: 1;
        display: flex;
        justify-content: center;
        align-items: center;
        color: rgb(215, 215, 215);
      }
    }
  }
}
</style>



