<!--
 * @Autor: linxu
 * @Date: 2022-04-27 14:00:36
 * @LastEditors: linxu
 * @LastEditTime: 2022-09-22 15:16:46
 * @Description: 控制器配置 表单
-->

<template>
  <el-form
    ref="configForm"
    label-width="180px"
    :class="{ 'config-form': true, 'is-show': !isEdit, 'v-form': type !== 'Horizontal' }"
    label-suffix=":"
  >
    <template v-if="isEdit">
      <el-form-item
        v-for="formItem in ruleForm.DetailList"
        :key="formItem.Id"
        :label="formItem.Name"
      >
        <!-- 下拉选择 -->
        <el-select v-if="formItem.IsBox && formItem.BoxList.length > 2" v-model="formItem['Value']">
          <el-option
            v-for="(optionItem, optionIndex) in formItem.BoxList"
            :key="optionIndex"
            :label="optionItem.Name"
            :value="optionItem.Value"
          ></el-option>
        </el-select>

        <el-radio-group
          v-else-if="formItem.IsBox && formItem.BoxList.length <= 2"
          v-model="formItem['Value']"
        >
          <el-radio
            v-for="(radioItem, radioIndex) in formItem.BoxList"
            :key="radioIndex"
            :label="radioItem.Value"
            >{{ radioItem.Name }}
          </el-radio>
        </el-radio-group>

        <!-- 数字计数器 -->
        <el-input-number
          v-else
          v-model="formItem['Value']"
          :min="formItem['MinValue']"
          :max="formItem['MaxValue']"
          :step="formItem['Precision']"
          controls-position="right"
        ></el-input-number>

        <!-- input -->
        <!-- <el-input
          v-if="formItem.type === 'input'"
          v-model="formItem['Value']"
          :placeholder="formItem.options.placeholder"
        ></el-input> -->

        <!-- radio -->
        <!-- <el-radio-group v-else-if="formItem.type === 'radio'" v-model="formItem['Value']">
          <el-radio
            v-for="(radioItem, radioIndex) in formItem.options.options"
            :key="radioIndex"
            :label="radioItem.value"
            >{{ radioItem.label }}</el-radio
          >
        </el-radio-group> -->

        <!-- date -->
        <!-- <el-date-picker
          v-else-if="formItem.type === 'date'"
          v-model="formItem['Value']"
          type="date"
          placement="bottom-start"
        >
        </el-date-picker> -->
        <div :class="['tips', type === 'Horizontal' ? '' : 'v-tips']">
          <span class="get-status">
            <em class="statusWriteIng" v-show="formItem.Status == '0'">写入中</em>
            <em class="statusReadIng" v-show="formItem.Status == '1'">读取中</em>
            <em class="statusFailed" v-show="formItem.Status == '3'">读取失败</em>
            <em class="statusFailed" v-show="formItem.Status == '4'">写入失败</em>
          </span>
          <span class="limit-box" v-if="!formItem['IsBox']">
            <em>({{ formItem['MinValue'] }}-{{ formItem['MaxValue'] }})</em>
            <em v-if="formItem['Unit']">{{ formItem['Unit'] }}</em>
            <em v-show="formItem.Precision == '1'">保留整数</em>
            <em v-show="formItem.Precision == '0.1'">保留一位小数</em>
            <em v-show="formItem.Precision == '0.01'">保留两位小数</em>
          </span>
        </div>
      </el-form-item>
    </template>
    <template v-else>
      <el-form-item
        v-for="formItem in ruleForm.DetailList"
        :key="formItem.Id"
        :label="formItem.Name"
      >
        <div class="show-form-item">
          <span>{{ !formItem['Value'] && formItem['Value'] !== 0 ? '-' : formItem['Value'] }}</span>
          <span class="unit-box" v-if="formItem['Unit']">{{ formItem['Unit'] }}</span>
          <span class="get-status">
            <em class="statusWriteIng" v-show="formItem.Status == '0'">写入中</em>
            <em class="statusReadIng" v-show="formItem.Status == '1'">读取中</em>
            <em class="statusFailed" v-show="formItem.Status == '3'">读取失败</em>
            <em class="statusFailed" v-show="formItem.Status == '4'">写入失败</em>
          </span>
        </div>
      </el-form-item>
    </template>
  </el-form>
</template>

<script>
export default {
  name: 'ConfigForm',
  props: {
    formData: {
      type: Object
    },
    isEdit: { type: Boolean },
    type: { type: String, default: 'Horizontal' }
  },
  data() {
    return {
      ruleForm: {},
      rules: {}
    }
  },
  watch: {
    formData: {
      handler: function (newVal, oldVal) {
        console.log('formData', newVal, oldVal)
        this.ruleForm = newVal
      },
      deep: true
    }
  },
  mounted() {},
  methods: {
    getFormData() {
      // 提交 必填项校验
      return new Promise((resolve, reject) => {
        const resData = this.ruleForm.DetailList.map((item) => {
          return {
            Address: item.Address,
            Value: !item.Value && item.Value !== 0 ? '' : item.Value
          }
        })
        resolve(resData)
      })
    }
  }
}
</script>

<style lang="less" scoped>
.config-form {
  // height: calc(100% - 44px);
  overflow: auto;
  padding-right: 8px;
  // padding: 16px 0 0;
  &.v-form .el-form-item {
    margin-bottom: 28px !important;
    .el-form-item__content {
      flex-wrap: wrap;
    }
  }
  &.is-show {
    /deep/ .el-form-item {
      margin-bottom: 0 !important;
    }
  }
  .show-form-item {
    .unit-box {
      padding-left: 4px;
    }
    .get-status {
      margin-left: 16px;
    }
  }
  .el-radio-group {
    .el-radio {
      line-height: 32px;
    }
  }
  .el-input-number,
  .el-select {
    width: 220px !important;
  }
  .tips {
    .get-status {
      margin-left: 16px;
    }
    &.v-tips {
      position: absolute;
      // right: 0;
      top: 34px;
      line-height: 20px;
      display: flex;
      .get-status {
        margin-left: 0;
      }
      .limit-box {
        margin-left: 8px;
        white-space: nowrap;
      }
      span > em {
        white-space: nowrap;
      }
    }
  }

  .get-status {
    .statusWriteIng {
      color: royalblue;
    }
    .statusReadIng {
      color: orange;
    }
    .statusFailed {
      color: red;
    }
  }
}
</style>



