<script>
/**
 * @desc country component
 * @auth Demon3443002624@qq.com
 * @date 2022-7-18 13:54:48
 * @desc refer to https://www.isqqw.com/echartsdetail?id=15377
 * @desc prototype design https://vo4xwk.axshare.com/#id=ihp963&p=%E8%BF%90%E7%BB%B4%E5%A4%A7%E5%B1%8F&g=1
 */

import Ranking from './Ranking.vue'
import color from '@/enum/color'

export default {
  name: 'Country',
  components: { Ranking },
  data() {
    return {
      zoom: 1,
      chart: null,
      mapData: [],
      barData: [],
      yAxisData: [],
      interval: 3000,
      timelineData: [],
      mapDataGroup: [],
      center: [113, 34],
      autoPlayFlag: true,
      dialogVisible: false,
      chartDataOriginal: [],
      // standard name&color mapping
      genSetStateOperationMapName: ['离线', '停机', '风险', '故障', '运行'],
      genSetStateOperationMapColor: [
        color.crewStatusMapping.offline,
        color.crewStatusMapping.stop,
        color.crewStatusMapping.warning,
        color.crewStatusMapping.error,
        color.crewStatusMapping.running
      ],
      genSetStateRentingMapName: ['待租', '租赁中', '维修', '施工', '检修'],
      genSetStateRentingMapColor: [
        color.rentingStatusMapping.idle,
        color.rentingStatusMapping.renting,
        color.rentingStatusMapping.repair,
        color.rentingStatusMapping.construct,
        color.rentingStatusMapping.check
      ],
      // renting state => timeline index
      genSetTimelineDataMapping: [1, 3, 5, 2, 4],
      // timeline index => renting state
      genSetTimelineDataMappingReverse: [-1, 0, 3, 1, 4, 2],
      genSetTimelineLabelMapping: ['全部', '待租赁', '施工', '租赁中', '检修', '维修']
      // auto play interval(default 5000 ms)
    }
  },
  props: ['refreshFlag', 'countryName'],
  computed: {},
  watch: {
    refreshFlag() {
      this.chart.setOption({
        geo: {
          zoom: this.zoom,
          center: this.center
        }
      })
    }
  },
  methods: {
    // keyboard event
    handleKeydown(e) {
      const keycode = e.code
      const f = this.autoPlayFlag
      if (keycode === 'Space') {
        this.chart &&
          this.chart.dispatchAction({
            type: 'timelinePlayChange',
            playState: !f
          })
        this.$message.success(f ? '已暂停自动展播！' : '已开始自动展播！')
        this.autoPlayFlag = !f
      }
    }
  },
  beforeCreate() {},
  created() {},
  beforeMount() {},
  mounted() {
    document.addEventListener('keydown', this.handleKeydown)
    this.$api.monitoringScreenOperation.findGensetProjectIndexMapCountryProvince().then((value) => {
      const responseData = value.Data
      let currentCountry = {}
      if (responseData && Array.isArray(responseData)) {
        responseData.forEach((e) => {
          if (e.Name === this.countryName) {
            currentCountry = e
          }
        })
      }
      if (!this.countryName)
        currentCountry = value.Data && value.Data && Array.isArray(value.Data) && value.Data[0]
      let data = currentCountry.GensetList
      let provinceList = currentCountry.TypeList
      let provinceListAll = currentCountry.TypeList
      let provinceListMapping = [
        'GensetCount',
        'GensetInfoStatus_0Count',
        'GensetInfoStatus_3Count',
        'GensetInfoStatus_1Count',
        'GensetInfoStatus_4Count',
        'GensetInfoStatus_2Count'
      ]
      let mapData = new Array()
      let mapDataGroup = [[], [], [], [], [], []]
      let yAxisData = [[], [], [], [], [], []]
      let barData = [[], [], [], [], [], []]
      let timelineData = []
      let timelineDataNum = [0, 0, 0, 0, 0, 0]
      if (data && Array.isArray(data)) {
        data.forEach((e) => {
          let obj = {
            genSetName: e.DisplayName,
            genSetStateOperation: e.RunStatus,
            genSetStateOperationName: this.genSetStateOperationMapName[e.RunStatus],
            genSetStateOperationColor: this.genSetStateOperationMapColor[e.RunStatus],
            genSetStateRenting: e.InfoStatus,
            genSetStateRentingName: this.genSetStateRentingMapName[e.InfoStatus],
            genSetStateRentingColor: this.genSetStateRentingMapColor[e.InfoStatus],
            genSetLocation: e.InstallationSite,
            genSetProject: e.ProjectName,
            ...e
          }
          let objMap = {
            name: obj.genSetName,
            value: [e.Longitude, e.Latitude, { ...obj }]
          }
          // renting state
          let index = e.InfoStatus
          let timelineIndex = this.genSetTimelineDataMapping[index]
          timelineDataNum[0]++
          timelineDataNum[timelineIndex]++
          mapData.push(objMap)
          mapDataGroup[timelineIndex].push(objMap)
        })
        mapDataGroup[0] = mapData.filter((e) => true)
        timelineDataNum.forEach((e, i) => {
          const timelineIndex = this.genSetTimelineDataMappingReverse[i]
          const color = i === 0 ? '#a0b7ef' : this.genSetStateRentingMapColor[timelineIndex]
          const labelStyle = {
            color: '#fff'
            // fontSize: 13
          }
          const obj = {
            value: this.genSetTimelineLabelMapping[i] + e,
            // item style
            itemStyle: {
              color: color
            },
            label: {
              ...labelStyle
            },
            emphasis: {
              label: {
                ...labelStyle
              }
            },
            // be selected
            checkpointStyle: {
              color: color,
              symbolSize: 28,
              borderWidth: 3
            },
            // in progress
            progress: {
              itemStyle: {
                color: color
              },
              label: {
                ...labelStyle
              }
            }
          }
          timelineData.push(obj)
        })
        this.mapData = mapData
        this.mapDataGroup = mapDataGroup
        this.timelineData = timelineData
      }
      if (provinceList && Array.isArray(provinceList)) {
        provinceListMapping.forEach((e, i) => {
          // negative order
          provinceList.sort((a, b) => b[e] - a[e])
          provinceList = provinceList.slice(0, 15)
          provinceList.reverse()
          provinceList.forEach((e2) => {
            if (Number(e2[e]) === 0) {
              yAxisData[i].push('')
              barData[i].push(null)
            } else {
              yAxisData[i].push(e2.Name)
              barData[i].push(e2[e])
            }
          })
        })
        this.yAxisData = yAxisData
        this.barData = barData
      }
      this.chart && this.chart.dispose()
      this.chart = this.$echarts.init(document.getElementById('ms-map-country'), null, {
        renderer: 'svg'
      })
      let optionXyMap01 = {
        timeline: {
          data: this.timelineData,
          axisType: 'category',
          autoPlay: true,
          symbolSize: 20,
          // symbolOffset: [0, -10],
          tooltip: {
            show: false
          },
          bottom: 20,
          label: {},
          loop: false,
          playInterval: this.interval //时间间隔
        },
        baseOption: {
          animation: true,
          animationDuration: 1000,
          animationEasing: 'cubicInOut',
          animationDurationUpdate: 1000,
          animationEasingUpdate: 'cubicInOut',
          grid: {
            right: '2%',
            top: '10%',
            bottom: '10%',
            width: '18%'
          },
          tooltip: {
            trigger: 'item',
            enterable: true,
            transitionDuration: 0.5,
            position: (point) => {
              return [point[0], point[1]]
            },
            backgroundColor: 'rgba(0,0,0,0.5)'
          },
          geo: {
            show: true,
            map: 'china',
            roam: true,
            zoom: this.zoom,
            center: this.center,
            label: {
              show: false
            },
            tooltip: {
              show: false
            },
            itemStyle: {
              areaColor: 'rgba(7, 16, 26)',
              borderColor: '#1cccff',
              borderWidth: 2,
              shadowColor: '#0f5d9d',
              shadowOffsetX: 0,
              // shadowOffsetY: 15,
              opacity: 0.5
            },
            emphasis: {
              itemStyle: {
                areaColor: '#1cccff'
              },
              label: {
                show: true,
                color: 'rgba(132, 212, 247)'
              }
            }
          }
        },
        options: []
      }
      for (let n = 0; n < this.timelineData.length; n++) {
        let mapDataNow = this.mapDataGroup[n]
        let mapDataNowOperation = mapDataNow.filter((e) => {
          if (e.value[2].InfoStatus.toString() === '1') return true
          else return false
        })
        let colorNow =
          n === 0
            ? '#a0b7ef'
            : this.genSetStateRentingMapColor[this.genSetTimelineDataMappingReverse[n]]
        let option = {
          backgroundColor: 'rgb(16,34,66)',

          title: [
            {
              id: 'statistic',
              text: `中国各省${this.genSetTimelineLabelMapping[n]}智能装备数量top 15 >`,
              right: '1%',
              top: '1%',
              textStyle: {
                color: '#FFF',
                fontSize: 18
              },
              triggerEvent: true
            }
          ],
          grid: {
            right: '5%',
            top: '12%',
            bottom: '10%',
            width: '150'
          },
          xAxis: {
            type: 'value',
            scale: true,
            position: 'top',
            min: 0,
            boundaryGap: false,
            splitLine: {
              show: false
            },
            axisLine: {
              show: false
            },
            axisLabel: {
              show: true,
              color: '#fff'
            },
            splitNumber: 4,
            max: (_) => {
              let max = _.max
              if (max <= 5) max = 5
              else if (max <= 10) max = 10
              else if (max <= 20) max = 20
              else if (max <= 50) max = 50
              else if (max <= 100) max = 100
              else if (max <= 200) max = 200
              else if (max <= 500) max = 500
              else max += 500
              return max
            }
          },
          yAxis: {
            type: 'category',
            axisLabel: {
              show: true,
              inside: false,
              overflow: 'truncate',
              width: 50,
              color: '#fff'
            },

            axisLine: {
              show: true,
              lineStyle: {
                color: '#fff'
              }
            },
            axisTick: {
              show: false
            },
            // top15
            data: this.yAxisData[n],
            z: 8
          },

          series: [
            // renting state
            {
              name: 'RentingStateEffectScatterChart',
              type: 'effectScatter',
              coordinateSystem: 'geo',
              data: mapDataNow,
              symbolSize: 12,
              label: {
                show: false
              },
              itemStyle: {
                color: (p) => {
                  const data = p.data
                  const rentingState = data.value[2].InfoStatus
                  const color = this.genSetStateRentingMapColor[rentingState]
                  return color
                }
              },
              tooltip: {
                show: true,
                formatter: (param) => {
                  const data = param.value[2]
                  return `
                    <div style="color:#fff;padding:5px;font-size:1.1rem">
                      <div style="font-size:1.2rem;padding:4px 0">${data.DisplayName}</div>
                      <div style="padding:4px 0">${
                        this.genSetStateOperationMapName[data.RunStatus]
                      }，
                      ${this.genSetStateRentingMapName[data.InfoStatus]}</div>
                      <div style="padding:4px 0">${data.InstallationSite}</div>
                      <div style="padding:4px 0">所属项目：${data.ProjectName}</div>
                    </div>
                `
                }
              },
              rippleEffect: {
                brushType: 'stroke',
                period: 5,
                scale: 5
              },
              zlevel: 2
            },
            {
              name: 'RankingBarChart',
              type: 'bar',
              itemStyle: {
                color: colorNow
              },
              barWidth: '50%',
              label: {
                show: true,
                position: 'right',
                color: '#fff'
              },
              tooltip: {
                show: false
              },
              data: this.barData[n]
            },
            {
              name: 'OperationStateScatterChart',
              type: 'scatter',
              coordinateSystem: 'geo',
              data: mapDataNowOperation,
              symbol: 'pin',
              symbolOffset: [0, -6],
              symbolSize: 28,
              label: {
                show: false
              },
              itemStyle: {
                color: (p) => {
                  const data = p.data
                  const operationState = data.value[2].RunStatus
                  const color = this.genSetStateOperationMapColor[operationState]
                  return color
                }
              },
              tooltip: {
                show: false
              },
              rippleEffect: {
                brushType: 'fill',
                color: 'green',
                period: 17,
                scale: 10
              },
              zlevel: 4
            },
            { type: 'bar', show: false, data: [0], name: '运行' },
            { type: 'bar', show: false, data: [0], name: '故障' },
            { type: 'bar', show: false, data: [0], name: '离线' },
            { type: 'bar', show: false, data: [0], name: '停机' },
            { type: 'bar', show: false, data: [0], name: '风险' }
          ]
        }
        if ([0, 3].includes(n)) {
          option.legend = {
            show: true,
            left: '5%',
            // icon: 'pin',
            // @/assets/icons/svg/location.svg:path
            icon: 'path://M461.312 15.36c-153.6 16.384-270.848 115.2-308.224 259.584-12.288 49.152-13.312 137.216-1.536 182.784 15.872 63.488 44.032 105.984 163.328 249.344 76.288 91.136 124.416 160.256 166.912 236.032 15.36 27.648 28.672 50.176 30.208 50.176 1.536 0 12.288-19.456 24.576-43.008 30.72-60.928 83.456-138.24 144.384-212.992 148.48-182.272 158.72-197.12 183.296-257.024 10.752-26.624 13.312-43.008 15.36-91.136 2.56-75.776-6.656-120.32-37.376-183.296C776.192 72.704 626.688-1.536 461.312 15.36z',
            bottom: '5%',
            // itemWidth: 23,
            itemHeight: 20,
            orient: 'vertical',
            selectedMode: false,
            textStyle: {
              color: '#fff',
              fontSize: 13,
              padding: [0, 0, 15, 0]
            },
            data: [
              {
                name: '运行',
                itemStyle: {
                  color: this.genSetStateOperationMapColor[4]
                }
              },
              {
                name: '故障',
                itemStyle: {
                  color: this.genSetStateOperationMapColor[3]
                }
              },
              {
                name: '离线',
                itemStyle: {
                  color: this.genSetStateOperationMapColor[0]
                }
              },
              {
                name: '停机',
                itemStyle: {
                  color: this.genSetStateOperationMapColor[1]
                }
              },
              {
                name: '风险',
                itemStyle: {
                  color: this.genSetStateOperationMapColor[2]
                }
              }
            ]
          }
        } else {
          option.legend = {
            show: false
          }
        }
        // if ([0, 3].includes(n)) {
        //   // operation state
        //   option.series.push({
        //     name: 'OperationStateScatterChart',
        //     type: 'scatter',
        //     coordinateSystem: 'geo',
        //     data: mapDataNowOperation,
        //     symbol: 'pin',
        //     symbolOffset: [0, -6],
        //     symbolSize: 28,
        //     label: {
        //       show: false
        //     },
        //     itemStyle: {
        //       color: (p) => {
        //         const data = p.data
        //         const operationState = data.value[2].RunStatus
        //         const color = this.genSetStateOperationMapColor[operationState]
        //         return color
        //       }
        //     },
        //     tooltip: {
        //       show: false
        //     },
        //     rippleEffect: {
        //       brushType: 'fill',
        //       color: 'green',
        //       period: 17,
        //       scale: 10
        //     },
        //     zlevel: 4
        //   })
        // }
        optionXyMap01.options.push(option)
      }
      this.chart.setOption(optionXyMap01)
      this.chart.on('timelineplaychanged', (e) => {
        const f = e.playState
      })
      this.chart.on('timelinechanged', (e) => {
        // back to word component
        if (e.currentIndex === 5)
          setTimeout((_) => {
            this.$emit('handleMapBackBtn')
          }, 3000)
      })
      this.chart.on('click', (e) => {
        this.chart.dispatchAction({
          type: 'timelinePlayChange',
          playState: false
        })
        e.componentType === 'title' && (this.dialogVisible = true)
        try {
          const text = e.event.target.style.text
          const textMapping = ['全部', '待租赁', '施工', '租赁中', '检修', '维修']
          let index = 0
          textMapping.forEach((e, i) => {
            if (text.includes(e)) index = i
          })
          const name = provinceListMapping[index]
          const chartDataOriginal = []
          provinceListAll.sort((a, b) => a[name] - b[name])
          provinceListAll.forEach((e2) => {
            const obj = {
              name: e2.Name,
              num: e2[name]
            }
            if (Number(e2[name]) === 0) {
              obj.name = ''
              obj.num = ''
            }
            chartDataOriginal.push(obj)
          })
          this.chartDataOriginal = chartDataOriginal
        } catch (err) {}
      })
    })
  },
  beforeUpdated() {},
  updated() {},
  beforeDestroy() {
    this.chart.clear()
    document.removeEventListener('keydown', this.handleKeydown)
  },
  destroyed() {}
}
</script>
<template>
  <div class="country-container">
    <div id="ms-map-country"></div>
    <el-dialog
      width="30%"
      :modal="true"
      :modal-append-to-body="false"
      title="数据详情"
      class="legend-dialog"
      :visible.sync="dialogVisible"
    >
      <ranking :chartDataOriginal="chartDataOriginal" :all="true"> </ranking>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<style lang='less' scoped>
.country-container {
  width: 100%;
  height: 100%;
  #ms-map-country {
    width: 100%;
    height: 100%;
  }
  /deep/.legend-dialog {
    .el-dialog__body {
      height: 70vh;
      overflow: scroll;
    }
  }
}
</style>


