                          </div>
                        </el-row>
                      </el-col>
                      <el-col :span="4">
                        <el-row type="flex" justify="center" align="middle">
                          <img
                            v-if="handleStateBtn('9')"
                            pointer
                            @click="handleControl('9')"
                            src="@/assets/img/MchMonitoring/ico_control_reset.png"
                          />
                        </el-row>
                        <el-row type="flex" justify="center" align="middle">
                          <div v-if="handleStateBtn('9')" class="control-text">报警复位</div>
                        </el-row>
                      </el-col>
                      <el-col :span="4">
                        <el-row type="flex" justify="center" align="middle">
                          <img
                            v-if="handleStateBtn('6')"
                            pointer
                            @click="handleControl('6')"
                            src="@/assets/img/MchMonitoring/ico_control_start.png"
                          />
                        </el-row>
                        <el-row type="flex" justify="center" align="middle">
                          <div v-if="handleStateBtn('6')" class="control-text">启动</div>
                        </el-row>
                      </el-col>
                    </el-row>
                  </el-col>
                  <!-- 2022-4-24 13:58:39 Demon：本页面暂不需要燃气控制 -->
                  <el-col :span="6" hidden>
                    <el-row class="gas-control">
                      <el-col :span="16">
                        <el-row type="flex" justify="center" align="middle">
                          <img src="@/assets/img/MchMonitoring/ControlvGas/line_up_true.png" />
                        </el-row>
                      </el-col>
                      <el-col :span="8">
                        <el-row class="true-false" type="flex" justify="center" align="middle">
                          <img src="@/assets/img/MchMonitoring/ControlvGas/ico_false.png" />
                        </el-row>
                      </el-col>
                      <el-col :span="8">
                        <el-row class="gas-icon-up" type="flex" justify="center" align="middle">
                          <img src="@/assets/img/MchMonitoring/ControlvGas/ico_gas_true.png" />
                        </el-row>
                      </el-col>
                    </el-row>
                    <el-row class="gas-control2">
                      <el-col :span="16">
                        <el-row type="flex" justify="center" align="middle">
                          <img src="@/assets/img/MchMonitoring/ControlvGas/line_down_true.png" />
                        </el-row>
                      </el-col>
                      <el-col :span="8">
                        <el-row class="true-false" type="flex" justify="center" align="middle">
                          <img src="@/assets/img/MchMonitoring/ControlvGas/ico_false.png" />
                        </el-row>
                      </el-col>
                      <el-col :span="8">
                        <el-row class="gas-icon-down" type="flex" justify="center" align="middle">
                          <img src="@/assets/img/MchMonitoring/ControlvGas/ico_gas_true.png" />
                        </el-row>
                      </el-col>
                    </el-row>
                    <el-row class="gas-control3">
                      <el-col :span="24">
                        <el-row type="flex" justify="center" align="middle">
                          <img
                            src="@/assets/img/MchMonitoring/ControlvGas/ico_control_gaslpg.png"
                          />
                        </el-row>
                        <el-row class="text" type="flex" justify="center" align="middle">
                          燃气切换
                        </el-row>
                      </el-col>
                    </el-row>
                  </el-col>
                </el-row>
              </el-card>
            </el-col>
          </el-row>
          <!-- 机组图片和视频、机组信息展示表单 -->
          <el-row :gutter="interval_card" class="crew-monitor-bottom">
            <el-col :span="12">
              <el-card :shadow="shadow">
                <el-row type="flex" justify="center">
                  <el-button :active="isPic" @click="isPic = !isPic">机组图片</el-button>
                  <el-button :active="!isPic" @click="isPic = !isPic">机组视频</el-button>
                </el-row>
                <div class="img-container" v-show="isPic">
                  <el-image
                    v-if="imagesUrls.length === 1"
                    fit="contain"
                    :style="{
                      height: '100%',
                      width: '100%'
                    }"
                    :preview-src-list="imagesUrls"
                    :src="imagesUrls[0]"
                  />
                  <el-carousel v-else indicator-position="outside">
                    <el-carousel-item v-for="(e, i) in imagesUrls" :key="i">
                      <el-image
                        fit="contain"
                        :style="{
                          height: '100%',
                          width: '100%'
                        }"
                        :preview-src-list="imagesUrls"
                        :src="e"
                      />
                    </el-carousel-item>
                  </el-carousel>
                </div>
                <div class="vidio-container" v-show="!isPic">
                  <!-- autoplay="autoplay" -->
                  <!-- muted 属性如果不添加，视频无法自动播放！-->
                  <!-- id="myPlayer" muted="muted" loop="loop" -->
                  <video
                    width="100%"
                    height="100%"
                    controls="controls"
                    ref="myVideo"
                    autoplay="autoplay"
                    muted="muted"
                  ></video>
                </div>
              </el-card>
            </el-col>
            <el-col :span="12">
              <el-card :shadow="shadow" class="form-box">
                <div>
                  <el-row type="flex" justify="start" align="middle">
                    ADC电压：<span>{{ infoDatas.cus_adc }}</span>
                  </el-row>
                  <el-row type="flex" justify="start" align="middle">
                    持续未发电时长：<span>{{ infoDatas.LastElectricityHour }}</span
                    >h
                  </el-row>
                  <el-row type="flex" justify="start" align="middle">
                    海拔高度：<span>{{ infoDatas.Altitude }}</span
                    >m
                  </el-row>
                  <el-row type="flex" justify="start" align="middle">
                    总流量（月）：<span>{{ infoDatas.MealValues }}MB</span>
                  </el-row>
                  <el-row type="flex" justify="start" align="middle">
                    已用流量：<span>{{ infoDatas.Values }}MB</span>
                  </el-row>
                </div>
                <div>
                  <el-row type="flex" justify="start" align="middle">
                    充电机电压：<span>{{ infoDatas.r_049 }}</span></el-row
                  >
                  <el-row type="flex" justify="start" align="middle">
                    运行时间：<span>{{ infoDatas.RunTime }}</span>
                  </el-row>
                  <el-row type="flex" justify="start" align="middle">
                    开机次数：<span>{{ infoDatas.BootTimes }}</span>
                  </el-row>
                  <el-row type="flex" justify="start" align="middle">
                    发电量统计：<span>{{ infoDatas.TotalElectricalEnergy }}</span
                    >kWh
                  </el-row>
                  <el-row type="flex" justify="start" align="middle">
                    机油温度：<span>{{ infoDatas.r_056 }}</span>
                  </el-row>
                </div>
              </el-card>
            </el-col>
          </el-row>
        </div>
      </el-main>
      <!-- <toggle-crew ></toggle-crew> -->
    </el-container>
  </div>
</template>

<script>
import Hls from 'hls.js'
import { mapGetters, mapActions } from 'vuex'

import Gauge from './components/Gauge'
import KeepIcon from './components/KeepIcon'
import ToggleCrew from '@/components/toggleCrew/list'
import ProgressBar from '@/components/ProgressBar'
import { handlePicUrl, formatInt, formatTime } from '@/utils/common'

export default {
  name: `CrewMonitor`,
  inject: ['reload'],
  components: {
    ToggleCrew,
    Gauge,
    ProgressBar,
    KeepIcon
  },
  data() {
    return {
      // 页面中卡片左右间隔
      interval_card: 10,
      // 页面卡片的阴影效果显示时机 always|hover|never
      shadow: 'hover',
      // 机组状态对应的颜色  0 离线 gray|1 停机 white|2 警告 orange|3 报警 red|4 运行 green
      stateColor: [`#808080`, `#f6f6f6`, `#f2c40d`, `#fd2e2d`, `#20be62`],
      mchStateCode: 4,
      mchStateName: '运行',
      timeArr: ['', '', ''],
      collectorTime: '',
      // 图标的大小状态
      iconHeight_underProgressBar: 20,
      iconHeight_underGauge: 16,
      // 发动机和负载进度条的高度
      heightProgressBar: 148,
      // 发电和市电进度条长度
      progressBarLengthVoltage: 0,
      // 选择的是机组图片还是机组视频
      isPic: true,
      mchName: null,
      // 机组图片地址列表
      imagesUrls: [],
      // 萤石云实时监控视频地址
      videoH5Url: '',
      value_temperature: 10,
      value2: 100,
      iconData: [
        {
          title: '79%',
          footer: `机油预供`
        },
        {
          title: '79%',
          footer: `机油预供`
        },
        {
          title: '79%',
          footer: `机油预供`
        },
        {
          title: '79%',
          footer: `机油预供`
        },
        {
          title: '79%',
          footer: `机油预供`
        },
        {
          title: '79%',
          footer: `机油预供`
        },
        {
          title: '79%',
          footer: `机油预供`
        },
        {
          title: '79%',
          footer: `机油预供`
        },
        {
          title: '79%',
          footer: `机油预供`
        },
        {
          title: '79%',
          footer: `机油预供`
        }
      ],
      // 定时器
      timer: null,
      // 图表最大值
      max: {},
      // 控制按钮
      control: {},
      // 实时数据
      realTimeData: {},
      // 实时报警数据
      alertList: [],

      axisLineSetting: [
        [0.86, '#228b22'],
        [0.93, '#f59a23'],
        [1, '#d9001b']
      ],
      /**
       * cus_adc	ADC电压
       * r_049	充电机电压
       * r_056	机油温度
       * 持续未发电时长	机组信息:LastElectricityHour
       * 海拔	机组信息:Altitude
       * 运行时间小时	机组信息:RunHour
       * 运行时间分钟	机组信息:RunMinute
       * 开机次数	机组信息:BootTimes
       * 发电量统计	机组信息:TotalElectricalEnergy
       */
      infoDatas: {
        cus_adc: '-',
        r_049: '-',
        r_056: '-',
        LastElectricityHour: '-',
        Altitude: '-',
        RunHour: '-',
        RunMinute: '-',
        RunTime: '-',
        BootTimes: '-',
        TotalElectricalEnergy: '-',
        // 流量的实用新型
        Values: '-',
        MealValues: '-'
      }
    }
  },
  props: ['id'],
  computed: {
    ...mapGetters(['selectCrew', 'isExpand'])
  },
  watch: {
    selectCrew: {
      handler: function (nv, ov) {
        nv && (this.mchName = nv.DisplayName)
        this._refresh()
      },
      deep: true
    },
    // 跳转路由对应页签与当前页签相同，组件复用，未重新查询数据
    $route: {
      handler: function (to, from) {
        if (from) {
          this.initData()
        }
      },
      deep: true,
      immediate: true
    }
    // 机组列表展开/收起 需要重置echart
    // isExpand: {
    //   handler: function (nv, ov) {
    //     this.reload()
    //   }
    // }
  },
  methods: {
    ...mapActions(['toggleSelectCrew']),

    initData() {
      this.getMchInfo()
      this.getPicVideo()
    },
    // 窗口大小改变时：1.更新进度条的长度
    _resize() {
      !!this.$refs.colVoltage &&
        !!this.$refs.colVoltage.$el &&
        (this.progressBarLengthVoltage = this.$refs.colVoltage.$el.clientWidth)
    },
    // 所选机组改变，更新数据
    _refresh() {
      // 刷新一次
      this.getMchInfo()
      this.getPicVideo()
      // 实时数据，定时轮询
      if (this.timer) {
        clearInterval(this.timer)
        this.timer = null
      }
      this.timer = setInterval(this.getRealTimeData, 3000)
    },
    // 全屏按钮
    handleFullscreen() {
      // window.open('/#/crew-monitor-info-fullscreen')
      let routeData = this.$router.resolve({
        path: '/crew-monitor-info-fullscreen'
      })
      window.open(routeData.href, '_blank')
    },
    // 控制按钮
    handleControl(n) {
      const content = `您确定按下此按钮吗?`
      const h = this.$createElement
      this.$confirm('操作提示', {
        title: '',
        message: h('div', [h('p', [h('span', content)])]),
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          const query = { id: this.selectCrew.ID, key: Number(n) }
          this.$api.operation
            .control({}, query)
            .then((res) => {
              this.$message.success(res.Message)
            })
            .catch((error) => {
              this.$message.error(error)
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          })
        })
      // this.$api.equipment.control({}, { id: this.selectCrew.ID, key: n }).then((resolve) => {
      // })
    },
    // 处理实时数据值和值加单位的显示
    handleValue(d, v = true) {
      let result = ''
      // 只返回值
      if (v) {
        try {
          result = handler(d.Value)
        } catch (err) {}
      }
      // 返回值+单位
      else {
        try {
          result = handler(d.Value) + d.Unit
        } catch (err) {}
      }
      result.length === 0 && (result = 0)
      function handler(value) {
        let result = Number(value)
        // 保留零位小数
        const decimal0 = [
          'r_050',
          'r_051',
          'r_052',
          'r_013',
          'r_014',
          'r_015',
          'r_010',
          'r_011',
          'r_012',
          'r_003',
          'r_004',
          'r_005',
          'r_000',
          'r_001',
          'r_002'
        ]
        // 保留一位小数
        const decimal1 = ['r_025', 'r_026', 'r_027']
        // 保留两位小数
        const decimal2 = ['r_044', 'r_019', 'r_009']
        if (d && d.No) {
          if (decimal0.includes(d.No)) {
            result = result.toFixed(0)
          } else if (decimal1.includes(d.No)) {
            result = result.toFixed(1)
          } else if (decimal2.includes(d.No)) {
            result = result.toFixed(2)
          }
        }
        return result
      }
      return result
    },
    // 处理控制按钮显示状态
    handleState(p) {
      if (!!this.realTimeData[p] && this.realTimeData[p].Value.toString() === '1') return true
      else return false
    },
    handleStateBtn(p) {
      let result = true
      if (!this.control.IsControl) result = false
      if (!this.control.ConfigControl || !this.control.ConfigControl[p]) result = false
      return result
    },
    /**
     * 合分闸开关的显示
     * 1.是否远程控制判断
     * 判断IsControl值，True可以控制
     * 2.合分闸单按钮判断
     * 判断CombinationBrakeType值，value=0为单按钮,value=1为双按钮
     */
    handleControlShow(t) {
      let flag = false
      const p = !t ? ['7', '10'] : ['8', '11']
      try {
        if (this.control.IsControl) {
          if (this.control.CombinationBrakeType.toString() === '0' && this.handleStateBtn(p[0]))
            flag = true
          if (
            this.control.CombinationBrakeType.toString() === '1' &&
            (this.handleStateBtn(p[0]) || this.handleStateBtn(p[1]))
          )
            flag = true
        }
      } catch (err) {}
      return flag
    },
    // 获取机组信息
    getMchInfo() {
      this.$api.screenData
        .getsingleMchInfo({ id: this.selectCrew.ID })
        .then((resolve) => {
          const data = resolve.Data
          // 计算图表最大值
          const max = {
            RatedSpeed: 2000,
            RatedPower: 500,
            RatedCurrent: 650,
            RatedPhaseVoltage: 286,
            RatedLineVoltage: 494
          }
          let timeArr = ['', '', '']
          let mchStateCode = 4
          let mchStateName = '运行'
          const infoDatas = this.infoDatas
          for (let ky in infoDatas) {
            const item = data[ky]
            if (!!item) infoDatas[ky] = item
          }
          if (infoDatas.LastElectricityHour) {
            infoDatas.LastElectricityHour = infoDatas.LastElectricityHour.toFixed(0)
          }
          if (infoDatas.RunMinute) {
            const t = Number(infoDatas.RunMinute)
            if (t % 60 === 0) {
              infoDatas.RunTime = t / 60 + 'h'
            } else {
              infoDatas.RunTime = (t / 60).toFixed(0) + 'h' + (t % 60) + 'min'
            }
          }
          /**
           * 1.转速仪表盘最大值:
           * 机组RatedSpeed > 0 &&RatedSpeed <= 2000       Max=2000
           * 机组RatedSpeed > 2000 &&RatedSpeed < 3000       Max=3000
           * 机组RatedSpeed >= 3000       Max=4000
           * 机组RatedSpeed ==0 ||RatedSpeed==null  Max=2000
           * 2.有功功率表盘最大值:
           * 机组RatedPower==0 ||RatedPower==null  Max=500
           * 如果RatedPower*1.3<500   Max=RatedPower*1.3向上取整50的倍数
           * 如果RatedPower*1.3>500   Max=RatedPower*1.3向上取整500的倍数
           * 3.电流最大值:
           * 机组RatedCurrent==0 ||RatedCurrent==null  Max=650
           * Max=RatedCurrent*1.3
           * 4.相电压最大值:
           * 机组RatedPhaseVoltage==0 ||RatedPhaseVoltage==null  Max=286（220*1.3）
           * Max=RatedPhaseVoltage*1.3
           * 5.线电压最大值:
           * 机组RatedLineVoltage==0 ||RatedLineVoltage==null  Max=494（380*1.3）
           * Max=RatedLineVoltage*1.3
           */
          try {
            if (Number(data.RatedSpeed) > 2000 && Number(data.RatedSpeed) < 3000)
              max.RatedSpeed = 3000
            if (Number(data.RatedSpeed) >= 3000) max.RatedSpeed = 4000

            if (Number(data.RatedPower) * 1.3 < 500)
              max.RatedPower = formatInt(Number(data.RatedPower) * 1.3, 50)
            if (Number(data.RatedPower) * 1.3 >= 500)
              max.RatedPower = formatInt(Number(data.RatedPower) * 1.3, 500)
            if (Number(data.RatedPower) === 0) max.RatedPower = 500

            if (Number(data.RatedCurrent) != 0) max.RatedCurrent = Number(data.RatedCurrent) * 1.3

            if (Number(data.RatedPhaseVoltage) != 0)
              max.RatedPhaseVoltage = Number(data.RatedPhaseVoltage) * 1.3

            if (Number(data.RatedLineVoltage) != 0)
              max.RatedLineVoltage = Number(data.RatedLineVoltage) * 1.3
          } catch (err) {}
          // 控制按钮
          const control = {
            // 是否显示控制按钮
            IsControl: false,
            // 显示哪些控制按钮
            ConfigControl: {},
            // 合分闸按钮数量
            CombinationBrakeType: 0
          }
          /**
           * 1.实时状态判断
           * 判断是否存在，不存在 直接false，如果存在，判断value，value=0为false,value=1为true
           * 2.是否远程控制判断
           * 判断IsControl值，True可以控制
           * 3.合分闸单按钮判断
           * 判断CombinationBrakeType值，value=0为单按钮,value=1为双按钮
           * 4.控制
           */
          try {
            if (data.IsControl) control.IsControl = true
            else control.IsControl = false
            if (data.ConfigControl) control.ConfigControl = data.ConfigControl
            if (data.CombinationBrakeType) control.CombinationBrakeType = data.CombinationBrakeType
          } catch (err) {}
          // 机组状态
          try {
            mchStateCode = data.Status
            mchStateName = data.StatusName
            timeArr = [
              formatTime(data.LastElectricityTime).format('YYYY-MM-DD HH:mm:ss'),
              formatTime(data.GeneratorStartStopRecordStartTime).format('YYYY-MM-DD HH:mm:ss'),
              formatTime(data.GeneratorStartStopRecordEndTime).format('YYYY-MM-DD HH:mm:ss')
            ]
          } catch (err) {}
          this.max = max
          this.control = control
          this.timeArr = timeArr
          this.mchStateCode = mchStateCode
          this.mchStateName = mchStateName
        })
        .catch((err) => {
          // 拦截器已有输出
          // this.$message({
          //   type: 'error',
          //   message: '网络请求失败，请检查网络！'
          // })
          console.error(
            '设备管理-机组监控-机组监控-获取实时数据接口 getsingleMchInfo ，错误信息：\n',
            err
          )
        })
    },
    // 获取机组图片（数组）和视频
    getPicVideo() {
      this.$api.screenData
        .FindGeneratorSetByMonitor({ id: this.selectCrew.ID })
        .then((resolve) => {
          const Data = resolve.Data
          // Data.ImagesUrls = [
          //   '../../Images/GeneratorSet/gensetdaaeed15-f776-428a-b64f-d81e9d44e5f6.jpg'
          //   // '../../AD/banner3.png'
          // ]
          !!Data.ImagesUrls &&
            (Data.ImagesUrls = Data.ImagesUrls.map((e) => {
              e = handlePicUrl(e, 'old')
              return e
            }))
          !!Data && (this.videoH5Url = Data.VideoH5Url)
          this.renderVideo()
          if (!Data.ImagesUrls || Data.ImagesUrls.length === 0) {
            this.imagesUrls = [require('@/assets/img/genset.png')]
          } else {
            this.imagesUrls = Data.ImagesUrls
          }
          this.infoDatas.MealValues = Data.FlowCollocation.MealValues
          this.infoDatas.Values = Data.FlowStatistics.Values.toFixed(2)
        })
        .catch((err) => {
          // 拦截器已有输出
          // this.$message({
          //   type: 'error',
          //   message: '网络请求失败，请检查网络！'
          // })
          console.error(
            '设备管理-机组监控-机组监控-获取图片和视频接口 FindGeneratorSetByMonitor ，错误信息：\n',
            err
          )
        })
    },
    // 获取实时数据
    getRealTimeData() {
      this.$api.screenData
        .getsingleMchInfoRealtime({ id: this.selectCrew.ID })
        .then((resolve) => {
          const nowData = resolve.Data.NowData
          let collectorTime = ''
          let alertList = []
          let realTimeData = {
            r_047: '',
            r_048: '',
            r_050: '',
            r_051: '',
            r_052: '',
            r_073: '',
            r_074: '',
            r_032: '',
            r_044: '',
            r_025: '',
            r_026: '',
            r_027: '',
            r_040: '',
            r_013: '',
            r_014: '',
            r_015: '',
            r_010: '',
            r_011: '',
            r_012: '',
            r_019: '',
            r_003: '',
            r_004: '',
            r_005: '',
            r_000: '',
            r_001: '',
            r_002: '',
            r_009: '',
            // 市电状态
            a_43_00: '',
            // 发电状态
            a_43_02: '',
            // 市电合分闸状态
            a_43_01: '',
            // 发电合分闸状态
            a_43_03: '',
            // 停机状态
            a_00_11: '',
            // 手动状态
            a_00_10: '',
            // 自动状态
            a_00_09: ''
          }
          for (const ky in realTimeData) {
            // 附空值供渲染
            const obj = {
              ServerTime: '',
              No: '',
              Describe: '',
              Unit: '',
              Type: '',
              TypeName: '',
              Value: '',
              NoKey: ''
            }
            realTimeData[ky] = obj
          }
          try {
            for (const ky in realTimeData) {
              if (!!nowData[ky]) realTimeData[ky] = nowData[ky]
            }
          } catch (err) {}
          try {
            alertList = [
              ...resolve.Data.ShutDownAlarms.map(
                (item) => formatTime(item.ServerTime).format('MM-DD HH:mm:ss') + ' ' + item.Describe
              ),
              ...resolve.Data.WarnAlarms.map(
                (item) => formatTime(item.ServerTime).format('MM-DD HH:mm:ss') + ' ' + item.Describe
              )
            ]
          } catch (err) {}
          try {
            collectorTime = formatTime(resolve.Data.CollectorTime).format('YYYY-MM-DD HH:mm:ss')
          } catch (err) {}
          this.realTimeData = realTimeData
          this.collectorTime = collectorTime
          this.alertList = alertList
          for (const ky in this.infoDatas) {
            const itemObj = nowData[ky]
            if (itemObj) {
              this.infoDatas[ky] = itemObj.Value + itemObj.Unit
            }
          }
        })
        .catch((err) => {
          // 拦截器已有输出
          // this.$message({
          //   type: 'error',
          //   message: '网络请求失败，请检查网络！'
          // })
          console.error(
            '设备管理-机组监控-机组监控-获取实时数据接口 getsingleMchInfoRealtime ，错误信息：\n',
            err
          )
        })
    },
    // 初始化萤石云视频播放（在刚进入页面时更新一次）
    renderVideo() {
      // 使用 hls 实现
      if (this.videoH5Url) {
        const hls = new Hls()
        hls.attachMedia(this.$refs.myVideo)
        hls.on(Hls.Events.MEDIA_ATTACHED, (_) => {
          hls.loadSource(this.videoH5Url)
          hls.on(Hls.Events.MANIFEST_PARSED, function () {
            this.$refs.myVideo.play()
          })
        })
        // 出错处理
        hls.on(Hls.Events.ERROR, function (event, data) {
          if (data.fatal) {
            switch (data.type) {
              case Hls.ErrorTypes.NETWORK_ERROR:
                // try to recover network error
                console.error('fatal network error encountered, try to recover')
                hls.startLoad()
                break
              case Hls.ErrorTypes.MEDIA_ERROR:
                console.error('fatal media error encountered, try to recover')
                hls.recoverMediaError()
                break
              default:
                // cannot recover
                hls.destroy()
                break
            }
          }
        })
      }
    }
  },
  mounted() {
    // 跳转新页面逻辑
    const query = this.$route.query
    let type = query && query.type
    let crewId = query && query.crewId
    if (type === 'newPage' && !!crewId) {
      this.$api.screenData.getsingleMchInfo({ id: crewId }).then((value) => {
        const data = value.Data
        if (data) {
          this.toggleSelectCrew(data)
          this._refresh()
          this._resize()
        }
      })
    } else {
      this._refresh()
      this._resize()
    }
    window.addEventListener('resize', this._resize)
  },
  // 清空 window 监听事件
  beforeDestroy() {
    window.removeEventListener('resize', this._resize)
    this._resize = null
    clearInterval(this.timer)
  }
}
</script>

<style lang="less" scoped>
// #crew-monitor {
// 每行内容的间距
@interval_card: 12px;
/deep/ .el-card {
  box-sizing: border-box;
}
// 卡片内容去掉内边距
/deep/ .el-card__body {
  padding: 0;
  // height: 260px;
  height: 240px;
}
// 卡片标题样式
/deep/ .el-card__header {
  // padding: 10px;
  padding: 6px;
  background: #f3f3f3;
  text-align: center;
  font-size: 1.1rem;
  color: #888888;
}

.crew-monitor {
  overflow: hidden;
  > .el-row {
    // padding: 0 15px;
    margin: 0 0 @interval_card 0;
    .el-row--flex {
      font-size: 12px;
    }
  }
  // > .el-row:nth-child(2),
  > .el-row:last-child {
    margin-bottom: 0;
  }
  &-top1 {
    // 隐藏机组选择组件多余的内容
    /deep/ p {
      display: none;
    }
  }
  &-top2 {
    margin: 0 0 @interval_card 0 !important;
    font-weight: bold;
    font-size: 0.8rem;
    padding: 0 0 0 15px;
    > div {
      position: absolute;
      right: 16px;
      top: -5px;
      padding: 5px;
      color: #fff;
      display: flex;
      cursor: pointer;
      font-size: 1rem;
      &:hover {
        transition: 0.3s;
        color: #1abc9c;
        // font-size: 1.2rem;
        // border: 1px solid #1abc9c4f;
      }
      svg {
        font-size: 1.2rem;
      }
    }
  }
  &-middle1 {
    svg {
      font-size: 1.7rem;
    }
    .offsetFuelPressure {
      padding: 2px 0 0 6px;
    }
    .offsetFuel {
      padding: 0 0 0 6px;
    }
    .gauge-box {
      // height: 220px;
      height: 200px;
      padding-top: 10px;
    }
    .gauge-label-box {
      transform: translateY(-16px);
      display: flex;
      align-items: center;
      margin-top: 6px;
    }
    .progress-bar-box {
      // 设置发动机和负载卡片进度条上下间距
      .progress-bar {
        margin: 2px auto;
      }
      // 设置文字顶部间距
      > div {
        margin-top: 10px;
      }
      .offsetFZ {
        margin-top: 14px;
      }
    }
    // 发电和市电样式
    .voltage-box {
      .el-row .is-align-middle {
        margin: 5px 0;
      }
    }
  }
  &-keep {
    /deep/ .el-card {
      margin: 0 5px;
    }
    /deep/ .el-card__body {
      height: auto;
      padding: 10px 20px;
      .a-item {
        height: 155px;
      }
      svg {
        font-size: 6rem;
      }
    }
  }
  &-middle2 {
    img[pointer] {
      cursor: pointer;
    }
    .left {
      .el-row:nth-child(1) {
        img {
          height: 66px;
        }
      }
      .el-row:nth-child(2) {
        img {
          height: 30px;
        }
      }
      .el-row:nth-child(3) {
        img {
          height: 50px;
        }
      }
    }
    .gas-control {
      > .el-col:first-child {
        padding: 16px 0 0 0;
      }
      img {
        width: 100%;
      }
      .true-false {
        position: absolute;
        left: 35%;
        top: 5px;
        img {
          width: 20px;
        }
      }
    }
    .gas-icon-up {
      padding: 0 0 0 3px;
    }
    .gas-icon-down {
      padding: 10px 0 0 3px;
    }
    .gas-control2 {
      > .el-col:first-child {
        padding: 5px 0 0 0;
      }
      img {
        width: 100%;
      }
      .true-false {
        position: absolute;
        left: 35%;
        top: 25px;
        img {
          width: 20px;
        }
      }
    }
    .gas-control3 {
      padding: 20px 0 0 0;
      img {
        width: 50px;
      }
      .text {
        color: #888888;
        font-size: 1.1rem;
      }
    }
    /deep/ .el-card__body {
      padding: 10px;
      // height: 200px;
      height: 170px;
    }
    .switch {
      transform: translateY(55%);
      height: 40px !important;
    }
    // 控制按钮前面的小圆点
    .btn {
      position: relative;
      transform: translateX(4px);
      font-size: 1.1rem;
      color: #888888;
      &::before {
        left: -12px;
        content: '';
        width: 10px;
        height: 10px;
        position: absolute;
        border-radius: 10px;
        background: #1abc9c;
        transform: translateY(50%);
      }
      &[class~='gray']::before {
        background: gray;
      }
    }
    .control-text {
      color: #888888;
      font-size: 1.1rem;
    }
    .alert-box {
      height: 100%;
      overflow: auto;
      > .el-row {
        border-bottom: 1px solid #ddd;
        padding: 5px;
      }
    }
  }
  &-bottom {
    flex: 1;
    height: 0;
    position: relative;
    .el-col {
      height: 100%;
      /deep/ .el-card {
        height: 100%;
        .el-card__body {
          height: 100%;
          .img-container,
          .vidio-container {
            position: relative;
            // height: calc(100% - 38px);
            height: 500px;
          }
        }
      }
    }
    /deep/ .el-card__body {
      padding: 5px;
      box-sizing: border-box;
      // height: 400px;
      // height: 208px;
      .el-button {
        margin: 0 0 6px 0;
        &:first-child {
          border-end-end-radius: 0;
          border-start-end-radius: 0;
        }
        &:last-child {
          border-end-start-radius: 0;
          border-start-start-radius: 0;
          border-left: 0;
        }
        &[active] {
          background: #1f927b !important;
          color: white !important;
        }
      }
    }
    /deep/.form-box {
      .el-card__body {
        display: flex;
        > div {
          width: 50%;
          .el-row {
            height: 42px;
            margin: 0 0 0 10px;
            border-bottom: 1px solid #ddd;
            &:last-child {
              border: 0;
            }
          }
        }
      }
    }
  }
  .title {
    padding: 5px;
    border-bottom: 1px solid #888888;
  }
}
</style>



