// 项目类型枚举
const ProjectTypeEnum = [
  { value: 1, label: '运维' },
  { value: 2, label: '代运维' }
]
// 项目状态枚举
const ProjectStatusEnum = [
  { value: 1, label: '准备中' },
  { value: 2, label: '进行中' },
  { value: 3, label: '已完成' }
]
// 合同文件类型枚举
const ProjectFileTypeEnum = [
  { value: 1, label: '合同' },
  { value: 2, label: '聊天截图' },
  { value: 3, label: '行程单' }
]

export default {
  /******** 项目列表页 ********/
  ProjectTypeEnum,
  ProjectStatusEnum,
  //表格头 数据
  listColumns: (that) => [
    {
      label: '项目名称',
      prop: 'ProjectName',
      minWidth: '35%'
    },
    {
      label: '项目类型',
      prop: 'ProjectType',
      minWidth: '20%',
      formatter: (row, column, index) => {
        return row.ProjectType
          ? ProjectTypeEnum.find((item) => item.value == row.ProjectType).label
          : ''
      }
    },
    {
      label: '项目编号',
      prop: 'ProjectNumber',
      minWidth: '35%'
    },
    {
      label: '功率需求',
      prop: 'TotalPower',
      minWidth: '20%'
    },
    {
      label: '机组数量',
      prop: 'Quantity',
      minWidth: '20%'
    },
    {
      label: '项目地址',
      prop: 'ProjectArea',
      minWidth: '40%'
    },
    {
      label: '项目创建时间',
      prop: 'CreateTime',
      minWidth: '30%',
      formatter: (row, column, index) => {
        return row.CreateTime ? that.$moment(row.CreateTime).format('yyyy-MM-DD HH:mm:ss') : ''
      }
    },
    {
      label: '项目状态',
      prop: 'Status',
      minWidth: '20%',
      formatter: (row, column, index) => {
        return row.Status ? ProjectStatusEnum.find((item) => item.value == row.Status).label : ''
      }
    },
    {
      label: '项目开始时间',
      prop: 'StartDate',
      minWidth: '30%',
      formatter: (row, column, index) => {
        return row.StartDate ? that.$moment(row.StartDate).format('YYYY.MM.DD') : ''
      }
    },
    {
      label: '项目结束时间',
      prop: 'EndDate',
      minWidth: '30%',
      formatter: (row, column, index) => {
        return row.EndDate ? that.$moment(row.EndDate).format('YYYY.MM.DD') : ''
      }
    },
    {
      label: '到期剩余时间',
      prop: 'EndDateLast',
      minWidth: '25%',
      formatter: (row, column, index) => {
        return row.EndDateLast || '已到期'
      }
    }
  ],

  /******** 项目详情页 ********/
  ProjectFileTypeEnum,
  /* 合同文件 表格头 数据 */
  contractColumns: (that) => [
    {
      label: '文件类型',
      prop: 'FileType',
      minWidth: '20%',
      formatter: (row, column, index) => {
        return row.FileType
          ? ProjectFileTypeEnum.find((item) => item.value == row.FileType).label
          : ''
      }
    },
    {
      label: '文件名称',
      prop: 'FileName',
      minWidth: '35%'
    },
    {
      label: '上传时间',
      prop: 'CreateTime',
      minWidth: '35%',
      formatter: (row, column, index) => {
        return row.CreateTime ? that.$moment(row.CreateTime).format('yyyy-MM-DD HH:mm:ss') : ''
      }
    },
    {
      label: '上传人',
      prop: 'CreateUserName',
      minWidth: '20%'
    },
    {
      label: '备注',
      prop: 'FileDetail',
      minWidth: '25%'
    }
  ],
  /* 合同文件查看 表格头 数据 */
  fileListColumns: (that) => [
    {
      label: '文件名称',
      prop: 'FileName',
      minWidth: '75%'
    },
    {
      label: '文件大小',
      prop: 'FileSize',
      minWidth: '25%'
    }
  ],

  /* 项目人员 表格头 数据 */
  personnelColumns: (that) => [
    {
      label: '公司类型',
      prop: 'CustomerType',
      minWidth: '35%',
      formatter: (row, column, index) => {
        return row.CustomerType
          ? that.CustomerTypeEnum.find((item) => item.value == row.CustomerType).label
          : ''
      }
    },
    {
      label: '公司名称',
      prop: 'CompanyName',
      minWidth: '25%'
    },
    // {
    //   label: '公司地址',
    //   prop: 'num',
    //   minWidth: '35%'
    // },
    // {
    //   label: '人员类型',
    //   prop: 'UserType',
    //   minWidth: '25%',
    //   formatter: (row, column, index) => {
    //     return row.UserType
    //       ? that.UserTypeEnum.find((item) => item.value == row.UserType).label
    //       : ''
    //   }
    // },
    {
      label: '负责人',
      prop: 'DisplayName',
      minWidth: '25%'
    },
    {
      label: '负责人电话',
      prop: 'Phone',
      minWidth: '25%'
    }
  ],

  /* 项目机组 表格头 数据 */
  crewColumns: (that) => [
    {
      label: '机组名称',
      prop: 'DisplayName',
      minWidth: '20%'
    },
    {
      label: '标识',
      prop: 'CollectorToken',
      minWidth: '25%'
    },
    {
      label: '发动机型号',
      prop: 'EngineModel',
      minWidth: '20%'
    },
    {
      label: '功率',
      prop: 'RatedPower',
      minWidth: '15%',
      unit: 'Kw'
    },
    {
      label: '运行时间',
      prop: 'RunHourTotal',
      minWidth: '15%',
      unit: 'h'
    },
    {
      label: '机组位置',
      prop: 'InstallationSite',
      minWidth: '25%',
      render: (h, parmas) => {
        return h(
          'span',
          {
            class: 'text-overflow-ellipsis MulLineHiding', //用类名控制文字溢出显示省略号
            attrs: {
              title: parmas.row.InstallationSite || ''
            }
          },
          parmas.row.InstallationSite
        )
      }
    },
    {
      label: '限功率',
      prop: 'limitPower',
      minWidth: '15%',
      unit: 'Kw'
    },
    {
      label: '是否并机',
      prop: 'IsParallel',
      minWidth: '15%',
      formatter: (row, column, index) => {
        return row.IsParallel ? '是' : '否'
      }
    },
    // {
    //   label: '电子围栏开启情况',
    //   prop: 'isOpen',
    //   minWidth: '18%',
    //   render: (h, parmas) => {
    //     return [
    //       h('div', {}, [
    //         h('el-switch', {
    //           props: {
    //             value: parmas.row.IsFenceOpen,
    //             disabled: that.projectStatus === 3
    //           },
    //           on: {
    //             input: (value) => {
    //               that.setElectronicFenceSwitch(parmas.row, value)
    //             }
    //           }
    //         }),
    //         h('br')
    //         // h(
    //         //   'span',
    //         //   { display: 'block' },
    //         //   parmas.row.IsFenceOpen ? `r=${parmas.row.IsFenceOpen}` : ''
    //         // )
    //       ])
    //     ]
    //   }
    // },
    // {
    //   label: '维保策略录入情况',
    //   prop: 'IsStrategy',
    //   minWidth: '18%',
    //   formatter: (row, column, index) => {
    //     return row.IsStrategy ? '已录入' : '未录入'
    //   },
    //   render: (h, parmas) => {
    //     return [
    //       h('div', {}, [
    //         h('el-switch', {
    //           props: {
    //             value: parmas.row.IsStrategy,
    //             disabled: that.projectStatus === 3
    //           },
    //           on: {
    //             input: (value) => {
    //               that.setStrategySwitch(parmas.row, value)
    //             }
    //           }
    //         })
    //       ])
    //     ]
    //   }
    // }
  ],
  selectCrewColumns: (that) => [
    {
      label: '机组名称',
      prop: 'DisplayName',
      minWidth: '20%'
    },
    // {
    //   label: '标识',
    //   prop: 'CollectorToken',
    //   minWidth: '20%'
    // },
    {
      label: '发动机型号',
      prop: 'EngineModel',
      minWidth: '25%'
    },
    {
      label: '功率',
      prop: 'RatedPower',
      minWidth: '20%',
      unit: 'Kw'
    },
    {
      label: '运行时间',
      prop: 'RunHourTotal',
      minWidth: '20%',
      unit: 'h'
    },
    {
      label: '机组位置',
      prop: 'InstallationSite',
      minWidth: '25%',
      render: (h, parmas) => {
        return h(
          'span',
          {
            class: 'text-overflow-ellipsis MulLineHiding', //用类名控制文字溢出显示省略号
            attrs: {
              title: parmas.row.InstallationSite || ''
            }
          },
          parmas.row.InstallationSite
        )
      }
    }
  ],
  /* 并机组配置 表格头 数据 */
  parallelColumns: (that) => [
    {
      label: '并机组主机名称',
      prop: 'ParallelJoinGensetIsMain',
      minWidth: '20%%'
    },
    {
      label: '并机组分机名称',
      prop: 'ParallelJoinGensetIsNotMain',
      minWidth: '50%'
    },
    // {
    //   label: '并机组主机标识',
    //   prop: 'ParallelJoinGensetIsMain',
    //   minWidth: '20%%'
    // },
    // {
    //   label: '并机组分机标识',
    //   prop: 'ParallelJoinGensetIsNotMain',
    //   minWidth: '50%'
    // },
    {
      label: '机组总数',
      prop: 'GensetTotal',
      minWidth: '10%'
    },
    {
      label: '并机组总功率',
      prop: 'num',
      minWidth: '20%',
      unit: 'Kw'
    }
  ],
  setParallelCrewColumns: (that) => [
    {
      label: '机组名称',
      prop: 'DisplayName',
      minWidth: '20%'
    },
    // {
    //   label: '标识',
    //   prop: 'CollectorToken',
    //   minWidth: '25%'
    // },
    {
      label: '发动机型号',
      prop: 'EngineModel',
      minWidth: '35%'
    },
    {
      label: '功率',
      prop: 'RatedPower',
      minWidth: '20%',
      unit: 'Kw'
    },
    {
      label: '运行时间',
      prop: 'RunHourTotal',
      minWidth: '20%',
      unit: 'h'
    }
  ]
}



