<!--
 * @Author: linxu
 * @Date: 2021-11-17 10:03:43
 * @LastEditors: linxu
 * @LastEditTime: 2022-08-19 16:09:42
 * @Description: 全局切换选中机组 列表展示组件
-->

<template>
  <!-- <el-aside> -->
  <el-drawer
    title=""
    v-if="isExpand"
    :visible.sync="isExpand"
    :direction="direction"
    :with-header="false"
    :before-close="handleClose"
  >
    <div class="toggle-crew-list">
      <p class="content-title">
        <span>机组列表</span>
        <i class="el-icon-d-arrow-right" @click="toggleIsExpandClick(false)"></i>
      </p>
      <el-row class="crew-status" type="flex">
        <el-col
          :span="4"
          :class="[
            `color_type_${item.value}`,
            searchForm.crewStatus === item.value ? 'active' : ''
          ]"
          v-for="item in statusList"
          :key="item.value"
        >
          <span @click="() => crewStatusChange(item.value)"></span>
          <em>{{ item.label }}</em>
        </el-col>
      </el-row>

      <div class="search-container">
        <el-input
          placeholder="请输入发电机组名称"
          v-model="searchForm.crewCode"
          class="input-with-select"
        >
          <el-button
            slot="append"
            type="primary"
            class="search-btn"
            icon="el-icon-search"
            @click="haddleSearchClick"
          ></el-button>
        </el-input>
      </div>

      <div class="genset-container">
        <div class="genset-list" v-if="crewList && crewList.length > 0">
          <!-- 给自定义组件标签添加原生事件 -->
          <el-row
            v-for="(item, index) in crewList"
            :key="index"
            @click.native="toggleCrew(item)"
            class="item-box"
            :class="{ active: item.ID === selectCrew.ID }"
          >
            <div>
              <div class="color_type">
                <span
                  :style="{
                    background: showByCrewStatus(item.Status).color
                  }"
                ></span>
              </div>
              <div class="name-text SingleLineHiding" :title="item.DisplayName">
                {{ item.DisplayName }}
              </div>
              <div v-if="item.Status != 0">
                <el-image
                  v-if="item.NetworkType == 2"
                  style="width: 20px; height: 20px"
                  :src="url"
                ></el-image>
                <el-image
                  v-else-if="item.NetworkType == 1"
                  style="width: 20px; height: 20px"
                  :src="getUrl(`${item.NetworkValue}`)"
                ></el-image>
              </div>
            </div>
            <div>
              <span class="status-text">
                {{ showByCrewStatus(item.Status).text }}
              </span>
              <span class="address-text SingleLineHiding" :title="item.InstallationSite">
                {{ item.InstallationSite }}
              </span>
            </div>
          </el-row>
        </div>
        <p class="no-data" v-else>暂无记录！</p>
        <!-- 分页部分 -->
        <el-pagination
          v-if="crewList && crewList.length > 0"
          background
          class="pagination"
          small
          :total="dataTotal"
          :page-size="pagination.pageSize"
          :current-page="pagination.currentPage"
          @size-change="handleChangeSize"
          @current-change="handleChangePage"
          :page-sizes="pagination.pageSizes"
          :pager-count="pagination.pagerCount"
          :layout="
            pagination && pagination.layout ? pagination.layout : 'total, prev, pager, next,jumper'
          "
        />
      </div>
    </div>
  </el-drawer>
  <div class="toggle-crew-noexpand" v-else>
    <p class="content-title-noexpand" @click="toggleIsExpandClick(true)">
      <i class="el-icon-d-arrow-left"></i>
      <span>机组列表</span>
    </p>
  </div>
  <!-- </el-aside> -->
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import PositionCascader from '@/components/PositionCascader'
import { showByCrewStatus } from '@/utils/common'

export default {
  name: 'toggle-crew',
  inject: ['reload'], // 注入App中注册的变量
  components: {
    PositionCascader
  },
  data() {
    return {
      direction: 'rtl',
      asideWidth: null,
      // statusText: ['离线', '停机', '警告', '报警', '运行'],
      statusList: [
        { value: 2, label: '报警' },
        { value: 3, label: '警告' },
        { value: 4, label: '运行' },
        { value: 5, label: '停机' },
        { value: 6, label: '离线' },
        { value: 0, label: '所有' }
      ], // 0 所有 2报警 3警告 4运行 5 停机 6离线
      selectCrewInfo: {}, // 已选中机组
      crewList: [], // 机组list数据
      searchForm: {
        crewCode: '', // 机组编码
        crewStatus: 0 // 机组状态
      },
      // 分页
      dataTotal: 0,
      pagination: {
        currentPage: 1,
        pageSize: 10,
        layout: 'prev, pager, next,jumper',
        pageSizes: [10, 20, 30, 40],
        pagerCount: 5
      },
      lastSearchForm: null,
      url: require('@/assets/img/crewmanage/ethernet_1.png'),

      searchData: [],
      showByCrewStatus
    }
  },
  computed: {
    ...mapGetters(['selectCrew', 'searchFormSave', 'isExpand', 'GensetBrowseRecords'])
  },
  watch: {
    // 监听选中机组切换
    selectCrew: {
      handler: function (newVal, oldVal) {
        console.log(newVal)
        this.selectCrewInfo = newVal
      },
      immediate: true,
      deep: true
    }
  },
  mounted() {
    this.selectCrewInfo = this.selectCrew

    if (this.searchFormSave) {
      this.searchForm.crewCode = this.searchFormSave.crewCode
      this.searchForm.crewStatus = this.searchFormSave.crewStatus
      this.$set(this.pagination, 'currentPage', this.searchFormSave.currentPage)
      this.$set(this.pagination, 'pageSize', this.searchFormSave.pageSize)
    }
    this.getCrewList()
  },
  methods: {
    ...mapActions([
      'toggleSelectCrew',
      'toggleSearchFormSave',
      'toggleIsExpand',
      'toggleGensetBrowseRecords'
    ]),
    getCrewList() {
      const query = {
        // IsWeb: true,
        page: this.pagination.currentPage,
        pageSize: this.pagination.pageSize
      }
      const params = {
        model: {
          DisplayMode: 1, // 1为所有启用，0为所有机组 2为所有禁用
          IsLease: 1, // 租赁
          UserID: -1,
          // CollectorToken: this.crewCode, // 标识
          StatusMode: this.searchForm.crewStatus, // 0 所有 2报警 3警告 4运行 5 停机 6离线
          InstallationSite: '', // 地址
          DisplayName: this.searchForm.crewCode // 名称
        }
      }
      this.$api.common.getCrewList(params, query).then((res) => {
        if (res.Code == 1) {
          this.crewList = res.Data.Data
          this.dataTotal = res.Data.TotalItemCount
          // this.loadData()
          console.log(
            res.Data &&
              res.Data.Data &&
              res.Data.Data.length > 0 &&
              !(this.selectCrewInfo && this.selectCrewInfo.ID)
          )
          if (
            res.Data &&
            res.Data.Data &&
            res.Data.Data.length > 0 &&
            !(this.selectCrewInfo && this.selectCrewInfo.ID)
          ) {
            console.log('初始化默认选中')
            this.selectCrewInfo = res.Data.Data[0]
            this.toggleSelectCrew(this.selectCrewInfo)
            this.toggleGensetBrowseRecords([this.selectCrewInfo])
            this.$nextTick(() => {
              console.log('初始化默认选中', this.GensetBrowseRecords)
            })
          }
        }
      })
    },
    getUrl(value) {
      return require(`@/assets/img/crewmanage/signal_${value}.png`)
    },
    // 切换机组状态
    crewStatusChange(value) {
      this.searchForm.crewStatus = value
      this.getCrewList()
      this.toggleSearchForm()
    },
    haddleSearchClick() {
      this.getCrewList()
      this.toggleSearchForm()
    },
    // 保存查询条件
    toggleSearchForm() {
      this.lastSearchForm = {
        crewCode: this.searchForm.crewCode,
        crewStatus: this.searchForm.crewStatus,
        currentPage: this.pagination.currentPage,
        pageSize: this.pagination.pageSize
      }
      this.toggleSearchFormSave(this.lastSearchForm)
    },
    // 切换选中机组
    toggleCrew(row) {
      if (row.ID) {
        const selectInfo = this.crewList.filter((item) => item.ID === row.ID)[0]

        this.toggleIsExpandClick(false)
        this.toggleSelectCrew(selectInfo)

        // 处理浏览足迹数据
        const findIndex = this.GensetBrowseRecords.findIndex((item) => item.ID === row.ID)
        if (findIndex === -1) {
          this.toggleGensetBrowseRecords([selectInfo, ...this.GensetBrowseRecords])
        } else {
          let newArr = _.cloneDeep(this.GensetBrowseRecords)
          newArr.splice(findIndex, 1)
          newArr.unshift(selectInfo)
          this.toggleGensetBrowseRecords(newArr)
        }

        this.$nextTick(() => {
          console.log('切换选中机组', this.GensetBrowseRecords)
        })
        return
      }
      this.$message({
        type: 'error',
        message: '请选择机组！'
      })
    },
    toggleIsExpandClick(val) {
      this.toggleIsExpand(val)
    },
    // 分页 当前页改变
    handleChangePage(val) {
      this.pagination.currentPage = val
      if (this.lastSearchForm) {
        this.searchForm = _.cloneDeep(this.lastSearchForm)
      }
      this.haddleSearchClick()
    },
    // 分页 每页条数改变
    handleChangeSize(val) {
      this.pagination.currentPage = 1
      this.pagination.pageSize = val
      if (this.lastSearchForm) {
        this.searchForm = _.cloneDeep(this.lastSearchForm)
      }
      this.haddleSearchClick()
    },

    handleClose(done) {
      // done()
      this.toggleIsExpandClick(false)
    }
  }
}
</script>

<style lang="less" scoped>
@import '~@/styles/variables.less';

.toggle-crew-list {
  height: calc(100% - 32px);
  margin: 16px 0 16px 16px;
  box-sizing: border-box;
  border: 1px solid #e4e4e4;

  display: flex;
  flex-direction: column;
  .content-title {
    margin-bottom: 0;
    padding-right: 16px;
    border-top: 0;
    border-left: 0;
    border-right: 0;

    i {
      float: right;
      margin-top: 13px;
      cursor: pointer;
    }
  }
  .crew-status {
    padding: 8px 0;
    .el-col {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      line-height: 20px;
      border-bottom: 1px solid transparent;
      span {
        width: 20px;
        height: 20px;
        margin-bottom: 4px;
        cursor: pointer;
      }
      &.active {
        border-color: @theme !important;
      }
    }
  }

  .search-container {
    margin: 10px 2px;
    /deep/ .input-with-select {
      .el-input-group__append {
        overflow: hidden;
        background-color: transparent;
      }
      .search-btn {
        border-radius: 0 4px 4px 0;
      }
    }
  }

  .genset-container {
    flex: 1;
    height: 0;
    display: flex;
    flex-direction: column;
    .genset-list {
      flex: 1;
      height: 0;
      overflow: auto;
    }
    /deep/.item-box {
      padding: 5px;
      border-bottom: 1px solid #e4e4e4;
      cursor: pointer;
      &:first-child {
        border-top: 1px solid #e4e4e4;
      }
      &.active {
        background-color: @theme; //  #46ae5a
        border-color: @theme;
      }
      > div {
        display: flex;
        justify-content: space-between;
        height: 20px;
        line-height: 20px;
      }
      > div:first-child {
        margin-bottom: 4px;
        .color_type {
          span {
            width: 20px;
            height: 20px;
            display: inline-block;
            margin: 0 6px;
          }
        }
        .name-text {
          flex: 1;
          margin: 0 8px;
        }
      }
      > div:last-child {
        span.status-text {
          width: 32px;
        }
        span.address-text {
          flex: 1;
          margin: 0 8px;
        }
      }
    }
    p.no-data {
      line-height: 60px;
      text-align: center;
    }
    .pagination {
      text-align: center;
      margin-top: 16px;
      padding: 8px 1px 16px;
      white-space: normal;
      .el-pagination .el-pager li {
        margin: 2px 4px !important;
      }
      /deep/.el-pagination__editor.el-input {
        .el-input__inner {
          height: 28px !important;
        }
      }
    }
  }

  // 机组状态对应标签颜色设置
  // 运行
  .color_type_4 {
    span {
      background: @crew_status_4;
    }
  }
  // 报警
  .color_type_2 {
    span {
      background: @crew_status_2;
    }
  }
  // 警告
  .color_type_3 {
    span {
      background: @crew_status_3;
    }
  }
  // 停机
  .color_type_5 {
    span {
      background: @crew_status_5;
    }
  }
  // 离线
  .color_type_6 {
    span {
      background: @crew_status_6;
    }
  }
  // 全部
  .color_type_0 {
    span {
      background: @crew_status_0;
    }
  }
}
.toggle-crew-noexpand {
  position: absolute;
  right: 0;
  top: 20%;
  // margin-left: 16px;
  width: 32px;
  border-radius: 8px;
  background-color: @theme;
  z-index: 1;
  .content-title-noexpand {
    padding: 12px 8px;
    text-align: center;
    cursor: pointer;
  }
}
.theme-dark {
  // .toggle-crew-list,
  .toggle-crew-list .genset-container .item-box {
    border-color: hsla(0, 0%, 100%, 0.3);
  }
  .el-drawer__wrapper {
    /deep/.el-drawer {
      background-color: @menuBg;
    }
  }
}
</style>



