            :min="formDataList[12]['MinValue']"
            :max="formDataList[12]['MaxValue']"
            :step="formDataList[12]['Precision']"
            controls-position="right"
          ></el-input-number>
          <p>
            <span class="get-status" v-if="formDataList[12].Status">
              <em :class="showStatusText(formDataList[12].Status).className">
                {{ showStatusText(formDataList[12].Status).label }}
              </em>
            </span>
            <span class="limit-box" v-if="!formDataList[12]['IsBox']">
              <em>({{ formDataList[12]['MinValue'] }}-{{ formDataList[12]['MaxValue'] }})</em>

              <em v-if="formDataList[12]['Unit']">{{ formDataList[12]['Unit'] }}</em>

              <em v-if="formDataList[12].Precision">
                {{ showPrecisionText(formDataList[12].Precision) }}
              </em>
            </span>
          </p>
        </div>
        <div class="show-form-item" v-else>
          <span>{{
            !formDataList[12]['Value'] && formDataList[12]['Value'] !== 0
              ? '-'
              : formDataList[12]['Value']
          }}</span>
          <p>
            <span class="get-status" v-if="formDataList[12].Status">
              <em :class="showStatusText(formDataList[12].Status).className">
                {{ showStatusText(formDataList[12].Status).label }}
              </em>
            </span>
          </p>
        </div>
        <span class="unit-box"> {{ formDataList[12].Unit || '-' }} </span> <span> 或 </span>
        <div class="input-container" v-if="isEdit">
          <el-input-number
            v-model="formDataList[13]['Value']"
            :min="formDataList[13]['MinValue']"
            :max="formDataList[13]['MaxValue']"
            :step="formDataList[13]['Precision']"
            controls-position="right"
          ></el-input-number>
          <p>
            <span class="get-status" v-if="formDataList[13].Status">
              <em :class="showStatusText(formDataList[13].Status).className">
                {{ showStatusText(formDataList[13].Status).label }}
              </em>
            </span>
            <span class="limit-box" v-if="!formDataList[13]['IsBox']">
              <em>({{ formDataList[13]['MinValue'] }}-{{ formDataList[13]['MaxValue'] }})</em>

              <em v-if="formDataList[13]['Unit']">{{ formDataList[13]['Unit'] }}</em>

              <em v-if="formDataList[13].Precision">
                {{ showPrecisionText(formDataList[13].Precision) }}
              </em>
            </span>
          </p>
        </div>
        <div class="show-form-item" v-else>
          <span>{{
            !formDataList[13]['Value'] && formDataList[13]['Value'] !== 0
              ? '-'
              : formDataList[13]['Value']
          }}</span>
          <p>
            <span class="get-status" v-if="formDataList[13].Status">
              <em :class="showStatusText(formDataList[13].Status).className">
                {{ showStatusText(formDataList[13].Status).label }}
              </em>
            </span>
          </p>
        </div>
        <span class="unit-box"> {{ formDataList[13].Unit || '-' }} </span> <span> 时进行提醒 </span>
      </div>
    </div>

    <div class="btn-container" v-if="!!formDataList && formDataList.length > 0">
      <el-button
        type="primary"
        v-if="!isEdit"
        @click="
          () => {
            this.$emit('handleRefreshClick')
          }
        "
        >刷新
      </el-button>
      <el-button
        type="primary"
        v-if="!isEdit"
        :disabled="formData.IsReadIng"
        @click="
          () => {
            this.$emit('handleReadClick')
          }
        "
        >读取
      </el-button>
      <template v-if="!formData.IsReadIng && !formData.IsWriteIng">
        <el-button type="primary" v-if="!isEdit" @click="handleEditClick">编辑 </el-button>
        <span v-else>
          <el-button @click="handleCancelClick">取消</el-button>
          <el-button type="primary" @click="handleSaveClick">保存</el-button>
        </span>
      </template>
    </div>
  </div>
</template>

<script>
export default {
  name: 'PowerLimitForm',
  props: {
    formData: {
      type: Object
    }
  },
  data() {
    return {
      isEdit: false,
      form: {},
      formDataList: []
    }
  },
  watch: {
    formData: {
      handler: function (newVal, oldVal) {
        console.log('formData', newVal, oldVal)
        this.form = newVal
        this.formDataList = _.cloneDeep(newVal.DetailList)
      },
      deep: true
    }
  },
  mounted() {},
  methods: {
    // 状态
    showStatusText(Status) {
      switch (Status) {
        case '0':
          return { className: 'statusWriteIng', label: '写入中' }
          break
        case '1':
          return { className: 'statusReadIng', label: '读取中' }
          break
        case '3':
          return { className: 'statusFailed', label: '读取失败' }
          break
        case '4':
          return { className: 'statusFailed', label: '写入失败' }
          break
        default:
          return { className: '', label: '' }
      }
    },
    // 精度
    showPrecisionText(Precision) {
      switch (Precision) {
        case 1:
          return '保留整数'
          break
        case 0.1:
          return '保留一位小数'
          break
        case 0.01:
          return '保留两位小数'
          break
        default:
          return ''
      }
    },
    // 点击编辑
    handleEditClick() {
      this.isEdit = true
    },

    // 点击取消
    handleCancelClick() {
      this.isEdit = false
      this.formDataList = _.cloneDeep(this.form.DetailList)
      this.$emit('handleRefreshClick')
    },

    // 点击保存
    handleSaveClick() {
      this.$refs.ConfigForm.getFormData().then((form) => {
        console.log('子组件传递的值', form)
        this.$emit('handleSave', form)
      })
    }
  }
}
</script>

<style lang="less" scoped>
.config-container {
  &.is-show {
    .config-item {
      margin-bottom: 16px;
      .show-form-item {
        > p {
          top: 24px;
          left: 50%;
          transform: translateX(-50%);
        }
      }
    }
  }
  .config-item {
    display: flex;
    line-height: 32px;
    margin-bottom: 36px;
    position: relative;
    &:last-child {
      margin-bottom: 48px;
    }
    &.select-item {
      > span {
        color: aquamarine;
      }
    }
    .item-check-status {
      margin-right: 8px;
    }
    .input-container {
      position: relative;
      display: flex;
      flex-direction: column;
      margin: 0 4px;
      .el-input-number {
        width: 100px;
      }
    }
    .input-container,
    .show-form-item {
      position: relative;
      > p {
        line-height: 24px;
        font-size: 12px;
        position: absolute;
        top: 32px;
        left: 50%;
        transform: translateX(-50%);
        white-space: nowrap;
        .get-status {
          .statusWriteIng {
            color: royalblue;
          }
          .statusReadIng {
            color: orange;
          }
          .statusFailed {
            color: red;
          }
        }
      }
    }
    .unit-box {
      margin: 0 4px;
    }
    .show-form-item {
      margin: 0 6px;
    }
  }
}
.btn-container {
  text-align: center;
}
</style>



