<template>
  <div class="strategy-config-container">
    <div class="config-container">
      <div class="config-container-title">
        <div class="content-item">
          <span class="tip"></span>
          <span>维保策略配置</span>

          <!-- <el-switch
            class="config-switch"
            v-model="IsOpne"
            @input="(val) => handleInput(val)"
            @click.native.prevent="handleClickSwitch()">
          </el-switch> -->

          <el-button
            v-show="isEditBase || isEdit"
            class="quote-btn"
            type="primary"
            @click="handleOpenStrategyDialog"
            >引用模板
          </el-button>
        </div>
      </div>
      <div class="config-container-content">
        <div class="config-container-content__left">
          <div class="content-item">
            <!-- <span class="tip"></span> -->
            <span>基础配置</span>
            <div
              class="btn-container"
              v-if="
                !!formDataList && !!formDataList.DetailList && formDataList.DetailList.length > 0
              "
            >
              <el-button
                v-if="!isEditBase"
                type="primary"
                @click="
                  () => {
                    this.$emit('handleRefreshClick')
                  }
                "
                >刷新
              </el-button>
              <el-button
                type="primary"
                v-if="!isEditBase"
                :disabled="formData.IsReadIng"
                @click="
                  () => {
                    this.$emit('handleReadClick')
                  }
                "
                >读取
              </el-button>

              <template v-if="!formData.IsReadIng && !formData.IsWriteIng">
                <el-button type="primary" v-if="!isEditBase" @click="handleEditClick"
                  >编辑
                </el-button>
                <span v-else>
                  <!-- <el-button type="primary" @click="handleOpenStrategyDialog">引用模板</el-button> -->
                  <el-button @click="handleCancelClick">取消</el-button>
                  <el-button type="primary" @click="handleSaveClick">保存</el-button>
                </span>
              </template>
            </div>
          </div>
          <!-- 表单数据 -->
          <config-form
            ref="ConfigForm"
            :isEdit="isEditBase"
            :formData="formDataList"
            :type="Type"
          ></config-form>
        </div>
        <div class="config-container-content__right">
          <div class="content-item">
            <!-- <span class="tip"></span> -->
            <span>维保事项</span>
            <div class="btn-container">
              <el-button type="primary" v-if="!isEdit" @click="handleEdit">编辑</el-button>
              <span v-else>
                <el-button @click="handleCancel">取消</el-button>
                <el-button type="primary" @click="handleSave()">保存</el-button>
                <!-- <el-button type="primary" @click="handleSave(1)">保存并启用</el-button> -->
              </span>
              <el-button
                type="primary"
                style="margin-left: 10px"
                v-show="
                  gensetSettingDetail &&
                  gensetSettingDetail.ProgramList &&
                  gensetSettingDetail.ProgramList.length > 0
                "
                @click="handleDelete"
                >删除</el-button
              >
            </div>
          </div>
          <ul class="maintenance-matter" v-if="runStatus && !isEdit">
            <li class="title">
              <div><span>维保事项</span></div>
              <div><span>运行时长(实际/策略)</span></div>
              <div><span>绝对时长(实际/策略)</span></div>
            </li>
            <template v-if="runStrategyItemList && runStrategyItemList.length > 0">
              <li v-for="(item, index) in runStrategyItemList" :key="index">
                <div>
                  <span class="SingleLineHiding" :title="item.ItemContent">
                    {{ item.ItemContent }}
                  </span>
                </div>
                <div>
                  <span>
                    <em>{{ item.NextRemindHour }}h</em> /
                    <em>{{ item.DefaultHourSetting }}h</em>
                  </span>
                </div>
                <div>
                  <span>
                    <em>{{ 0 }}天</em> /
                    <em>{{ item.DefaultDaySetting }}天</em>
                  </span>
                </div>
              </li>
            </template>
            <li v-else class="no-data"><p>暂无数据</p></li>
          </ul>
          <edit-table
            v-if="(!runStatus || (runStatus && isEdit)) && flag"
            ref="editTable"
            :table_columns="table_columns"
            :dataArray="StrategyItemList"
            :isEdit="isEdit"
            @dataChange="tableDataChange"
          />
        </div>
      </div>
    </div>

    <!-- 选择模板弹窗 -->
    <SelectStrategyContent
      v-if="selectDialigVisible"
      @handleConfirmQuote="handleConfirmQuote"
      @closeDialog="closeDialog"
    ></SelectStrategyContent>
  </div>
</template>

<script>
import ConfigForm from './ConfigForm.vue'
import EditTable from './EditTable/index.vue'
import SelectStrategyContent from './SelectStrategyContent.vue'
const table_columns = [
  {
    prop: 'ItemContent',
    label: '维保事项',
    width: '40%'
  },
  {
    prop: 'DefaultHourSetting',
    label: '运行时间(h)',
    width: '30%'
  },
  {
    prop: 'DefaultDaySetting',
    label: '绝对时间(天)',
    width: '30%'
  }
]

export default {
  name: 'StrategyConfigForm',
  components: {
    ConfigForm,
    EditTable,
    SelectStrategyContent
  },
  props: {
    formData: {
      type: Object
    },
    gensetId: {
      type: Number,
      default: null
    },
    runStatus: {
      type: Boolean,
      default: null
    }
  },
  data() {
    return {
      Type: 'Vertical',
      // GensetId: null,
      formDataList: null,
      isEditBase: false,
      isEdit: false,
      table_columns: table_columns,
      setStrategyType: null,
      // strategyId: null,
      StrategyItemList: null,
      ProgramList: null,
      gensetSettingDetail: null,
      StrategyId: null, // 策略模板ID
      flag: false,

      IsOpne: false,
      selectDialigVisible: false,
      selectStrategyData: null,

      runStrategyItemList: null //  维保事项和提醒
    }
  },
  watch: {
    formData: {
      handler: function (newVal, oldVal) {
        this.formDataList = newVal
      },
      deep: true
    },
    // 监听选中机组切换
    gensetId: {
      handler: function (newVal, oldVal) {
        this.getGensetSettingDetail()
        if (this.runStatus) {
          this.getGensetSettingDetailV2()
        }
        this.isEditBase = false
        this.isEdit = false
      },
      deep: true
    }
  },
  mounted() {
    this.getGensetSettingDetail()
    if (this.runStatus) {
      this.getGensetSettingDetailV2()
    }
  },
  methods: {
    /**
     * 基础配置
     */
    // 点击编辑
    handleEditClick() {
      this.isEditBase = true
    },

    // 点击取消
    handleCancelClick() {
      this.isEditBase = false
      this.$emit('handleRefreshClick')
    },

    // 点击保存
    handleSaveClick() {
      this.$refs.ConfigForm.getFormData().then((form) => {
        this.$emit('handleSave', form)
        this.isEditBase = false
      })
    },

    /**
     * 维保策略配置
     */
    // 查询维保策略详情
    getGensetSettingDetail() {
      this.$api.project
        .GetMaintenanceGensetSettingDetail({}, { GensetId: this.gensetId })
        .then((res) => {
          if (res.Code === 1) {
            this.StrategyId = res.Data.StrategyId
            this.$set(this, 'gensetSettingDetail', res.Data)
            this.$set(this, 'StrategyItemList', res.Data.ProgramList)
            this.flag = true
          } else {
            this.$message({
              type: 'error',
              message: '机组维保策略查询失败！'
            })
          }
        })
    },

    // 查询维保策略以及提醒
    getGensetSettingDetailV2() {
      this.$api.screenData
        .GetMaintenanceGensetSettingDetailV2({}, { GensetId: this.gensetId })
        .then((res) => {
          // console.log(res)
          if (res.Code === 1) {
            // 维保事项
            this.$set(this, 'runStrategyItemList', res.Data.Programs)
          }
        })
    },

    tableDataChange(val) {
      this.$set(this, 'ProgramList', val.data)
    },
    // 点击编辑
    handleEdit() {
      this.isEdit = true
    },

    // 点击取消
    handleCancel() {
      this.getGensetSettingDetail()
      if (this.runStatus) {
        this.getGensetSettingDetailV2()
      }
      this.isEdit = false
    },

    // 保存
    handleSave() {
      if (
        !this.ProgramList ||
        (this.ProgramList && this.ProgramList.length == 0) ||
        this.ProgramList.filter((data) => data.MaintenanceItemId !== -1).length == 0
      ) {
        this.$message({
          type: 'warning',
          message: '请添加维保事项!'
        })
        return
      }
      for (let i = 0; i < this.ProgramList.length; i++) {
        let dataItem = this.ProgramList[i]
        if (!dataItem.ItemContent || !dataItem.DefaultHourSetting || !dataItem.DefaultDaySetting) {
          this.$message({
            type: 'warning',
            message: '维保事项不允许保存空值!'
          })
          return
        }
      }
      this.editSave()
    },

    editSave() {
      const ItemList = this.ProgramList.map((item) => {
        return {
          MaintenanceProgramId: item.MaintenanceProgramId,
          MaintenanceItemId: item.MaintenanceItemId,
          ItemContent: item.ItemContent,
          DefaultHourSetting: item.DefaultHourSetting,
          DefaultDaySetting: item.DefaultDaySetting
        }
      })
      const params = {
        StrategyId: this.StrategyId,
        ProgramList: ItemList
      }
      const query = { GensetId: this.gensetId }
      this.$api.screenData.EditMaintenanceGensetSettingV2(params, query).then((res) => {
        if (res.Code === 1) {
          this.$message({
            type: 'success',
            message: res.Message
          })
          this.handleCancel()
        }
      })
    },

    // 删除
    handleDelete() {
      const h = this.$createElement
      this.$confirm('操作提示', {
        title: '关闭维保策略',
        message: h('div', [h('p', [h('span', '确认删除数据吗？')])]),
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(() => {
          const query = { GensetId: this.gensetId }
          this.$api.project
            .CloseMaintenanceGensetSetting({}, query)
            .then((res) => {
              this.handleCancel()
              this.$message.success(res.Message)
            })
            .catch((error) => {
              this.$message.error(error)
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          })
        })
    },

    // 引用模板
    handleOpenStrategyDialog() {
      this.selectDialigVisible = true
    },
    // 确认引用
    handleConfirmQuote(obj) {
      // console.log('确认引用', obj)
      this.selectStrategyData = obj
      this.StrategyId = obj.StrategyId
      // 基本信息
      // this.formData.IsReadSuccess
      if (!this.formData.IsReadIng && !this.formData.IsWriteIng) {
        let list = this.formData.DetailList.map((item) => {
          if (item.Name == '维保节拍时长（单位h）') {
            item.Value = obj.BeatLength
          } else if (item.Name == '维保周期节拍个数') {
            item.Value = obj.BeatCount
          } else if (item.Name == '检修周期个数') {
            item.Value = obj.CycleCount
          }
          return item
        })
        this.$set(this.formData, 'DetailList', list)
        this.isEditBase = true
      } else {
        this.$message({
          type: 'warning',
          message: '基本信息引用数据失败!'
        })
      }

      // 维保事项 原数据处理 需要删除（MaintenanceItemId设置为-1）
      this.isEdit = true
      let data = this.ProgramList.filter((Item) => Item.MaintenanceItemId !== null).map((item) => {
        return {
          ...item,
          MaintenanceItemId: -1
        }
      })
      this.$set(this, 'StrategyItemList', data.concat(obj.StrategyItemList))
    },
    // 关闭弹窗
    closeDialog() {
      this.selectDialigVisible = false
    }
  }
}
</script>

<style lang="less" scoped>
@import '~@/styles/variables.less';
.strategy-config-container {
  height: 100%;
  .config-container {
    height: 100%;
    display: flex;
    flex-direction: column;
    border: 1px solid #e7e7e7;
    box-sizing: border-box;
    &-title {
      line-height: 40px;
      padding: 4px 8px;
      border-bottom: 1px solid #e7e7e7;
      .config-switch {
        margin-left: 16px;
        /deep/.el-switch__core {
          width: 60px;
        }
      }
      .quote-btn {
        margin-left: 16px;
      }
    }
    &-content {
      flex: 1;
      height: 0;
      display: flex;
      // min-height: 320px;
      // max-height: 610px;
      > div {
        padding: 16px;
        border-radius: 4px;
        display: flex;
        flex-direction: column;
        // border: 1px solid #e7e7e7;
        .content-item {
          position: relative;
          line-height: 32px;
          margin-bottom: 12px;
          .btn-container {
            position: absolute;
            right: 0;
            top: 0;
          }
        }
      }
      &__left {
        width: 38%;
        // margin-right: 24px;
      }
      &__right {
        width: 0;
        flex: 1;
        /deep/ .table-container {
          flex: 1;
        }

        // 维保事项
        .maintenance-matter {
          flex: 1;
          margin: 8px 0;
          li {
            display: flex;
            height: 40px;
            line-height: 40px;
            border-bottom: 1px solid hsla(0, 0%, 100%, 0.3);
            &.title {
              background: #f3f3f3 !important;
            }
            div {
              width: 33.33%;
              padding: 0 4px;
              display: flex;
              justify-content: center;
              align-items: center;
              > span {
                line-height: 20px;
              }
            }
            &.no-data p {
              flex: 1;
              text-align: center;
            }
          }
        }
      }
    }
  }
}
.theme-dark .strategy-config-container .config-container {
  border-color: @borderColor;
  > div {
    border-color: @borderColor;
  }
  .maintenance-matter {
    li.title {
      background: #304156 !important;
    }
  }
}
</style>



