export const paginationConfigMixin = {
  data() {
    return {
      dataTotal: 0,
      paginationCfg: {
        currentPage: 1,
        pageSize: 10,
        layout: 'total, sizes, prev, pager, next, jumper',
        pageSizes: [10, 20, 30, 40],
        pagerCount: 5
      },
      lastSearchForm: null
      // searchForm: {}
    }
  },
  created() {},
  mounted() {
    if (this.searchForm) {
      this.lastSearchForm = _.cloneDeep(this.searchForm)
    }
  },
  methods: {
    // 分页 当前页改变
    handleChangePage(val) {
      console.log(`当前页: ${val}`, this.lastSearchForm)
      this.paginationCfg.currentPage = val
      if (this.lastSearchForm) {
        this.searchForm = _.cloneDeep(this.lastSearchForm)
      }
      this.queryTableData()
    },
    // 分页 每页条数改变
    handleChangeSize(val) {
      console.log(`每页 ${val} 条`)
      this.paginationCfg.currentPage = 1
      this.paginationCfg.pageSize = val
      if (this.lastSearchForm) {
        this.searchForm = _.cloneDeep(this.lastSearchForm)
      }
      this.queryTableData() //获取表格数据的方法
    },
    // 重置分页
    resetCurrentParams() {
      this.paginationCfg.currentPage = 1
      this.paginationCfg.pageSize = 10
      if (this.searchForm) {
        this.lastSearchForm = _.cloneDeep(this.searchForm)
      }
      this.queryTableData()
    },
    // 序号递增
    handleChangeIndex(val) {
      if (this.paginationCfg) {
        return (this.paginationCfg.currentPage - 1) * this.paginationCfg.pageSize + val + 1
      }
      return val + 1
    }
  },
  watch: {
    // 对表格数据进行删除时，而且是删除到该页最后一条数据时，当前页面currentPage并不能自动减1
    dataTotal() {
      if (
        this.dataTotal == (this.paginationCfg.currentPage - 1) * this.paginationCfg.pageSize &&
        this.dataTotal !== 0
      ) {
        this.paginationCfg.currentPage = 1
        this.queryTableData()
      }
    }
  }
}



