<template>
  <div>
    <el-dialog
      title="单机工程进度记录"
      class="progress-records-dialog"
      width="60%"
      :visible.sync="dialigVisible"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      :append-to-body="true"
      @close="close()"
    >
      <div class="dialog-content">
        <!-- 操作按钮 -->
        <!-- <div class="operation-box">
          <el-button type="primary" @click="addProgressRecord()">添 加</el-button>
        </div> -->
        <TableList
          class="file-list-table"
          :options="options"
          :columns="columns"
          :dataSource="dataSource"
          border
          v-loading="loading"
        >
          <el-table-column
            slot="operation"
            label="操作"
            align="center"
            v-bind="options && options.props"
          >
            <template slot-scope="scope">
              <div class="operate-group">
                <el-button type="text" @click="showProgressRecord(scope.row)"> 查看 </el-button>
                <!-- <el-button type="text" @click="handleDownloadClick(scope.row)"> 下载 </el-button> -->
                <!-- <el-button type="text" @click="deleteProgressRecord(scope.row)"> 删除 </el-button> -->
              </div>
            </template>
          </el-table-column>
        </TableList>
      </div>
    </el-dialog>

    <!-- 记录表单弹窗 -->
    <RecordFormDialog
      v-if="RecordFormDialogVisible"
      :ProjectProgressId="ProjectProgressId"
      :RecordId="RecordId"
      :OperateType="OperateType"
      @closeDialog="(val) => closeDialog('RecordFormDialogVisible', val)"
    />
  </div>
</template>

<script>
import RecordFormDialog from './RecordFormDialog.vue'
import _data from './data.js'

export default {
  name: 'GensetProgressRecordsDialog',
  props: {
    ProjectProgressId: {
      type: Number,
      default: null
    },
    ProjectId: {
      type: Number,
      default: null
    },
    GensetId: {
      type: Number,
      default: null
    }
  },
  components: {
    RecordFormDialog
  },
  data() {
    return {
      loading: false,
      dialigVisible: true,

      // ProjectId: parseInt(this.$route.query.ProjectId),
      // GensetId: parseInt(this.$route.query.GensetId),

      columns: _data.gensetProgressRecordsColumns(this),
      // 表格
      options: {
        index: true, //序号列
        labelIndex: '序号',
        props: {
          width: 180 // 操作列宽度
        }
      },
      dataSource: [],

      RecordFormDialogVisible: false, // 记录表单弹窗
      OperateType: null, // 操作类型 1--添加 2--查看
      RecordId: null // 记录ID
    }
  },
  mounted() {
    this.queryTableData()
  },
  methods: {
    // 查询项目进度记录
    queryTableData() {
      this.loading = true
      const params = {
        ProjectId: this.ProjectId,
        ProgressId: this.ProjectProgressId,
        GeneratorSetId: this.GensetId
      }
      this.$api.project
        .GetGensetProjectProgressRecord(params)
        .then((res) => {
          if (res.Code === 1) {
            console.log(res)
            this.$set(this, 'dataSource', res.Data)
          }
          this.loading = false
        })
        .catch(() => {
          this.loading = false
        })
    },
    close() {
      this.$emit('closeDialog')
    },

    // 添加记录
    addProgressRecord() {
      this.RecordId = null
      this.OperateType = 1
      this.RecordFormDialogVisible = true
    },

    // 查看记录
    showProgressRecord(row) {
      this.RecordId = row.RecordId
      this.OperateType = 3
      this.RecordFormDialogVisible = true
    },

    // 删除
    deleteProgressRecord(row) {
      console.log(row)
    },

    // 下载
    handleDownloadClick(row) {
      console.log(row)
    },

    // 弹框组件关闭
    closeDialog(key, val) {
      this[key] = false
      if (val && val.refresh) {
        this.queryTableData()
      }
    }
  }
}
</script>

<style lang="less" scoped>
.progress-records-dialog {
  /deep/.el-dialog {
    min-height: 400px;
    .el-dialog__body {
      padding: 25px;
    }
  }
  .operation-box {
    margin-bottom: 16px;
  }
}
</style>



