<template>
  <div class="number-box" :title="title">
    {{ totalNum === 0 ? `-` : number }}
  </div>
</template>

<script>
export default {
  props: {
    number: {
      type: String,
      default: '0'
    },
    // 总数为0，则显示'-'
    totalNum: {
      type: Number,
      default: 0
    },
    title: {
      type: String,
      default: ''
    }
  }
}
</script>

<style lang="less" scoped>
.number-box {
  width: 50px;
  height: 80px;
  display: flex;
  font-size: 4rem;
  border-radius: 5px;
  align-items: center;
  justify-content: center;
  background-color: #032a54;
}
</style>



