<!--
 * @Autor: linxu
 * @Date: 2022-01-19 15:10:11
 * @LastEditors: linxu
 * @LastEditTime: 2022-09-02 17:22:49
 * @Description: 多 Y 轴 echarts图例组件
-->
<template>
  <div :id="containerId"></div>
</template>

<script>
import { mapGetters } from 'vuex'
const opt = function (val, theme) {
  const option = {
    textStyle: {
      color: theme === 'dark' ? 'rgba(255, 255, 255, 1)' : 'rgba(110, 112, 121, 1)'
    },
    tooltip: val.tooltip,
    grid: val.grid,
    toolbox: {
      iconStyle: {
        normal: {
          borderColor: theme === 'dark' ? 'rgba(255, 255, 255, 1)' : 'rgba(110, 112, 121, 1)'
        }
      },
      feature: {
        dataView: { show: val.toolboxCfg.dataViewIsShow || false, readOnly: false },
        magicType: { show: val.toolboxCfg.magicTypeIsShow || false, type: ['line', 'bar'] },
        restore: { show: val.toolboxCfg.restoreIsShow || false },
        saveAsImage: { 
          backgroundColor: theme === 'dark' ? "rgba(16, 36, 71, 1)" : 'auto',
          show: val.toolboxCfg.saveAsImageIsShow || false
        }
      }
    },
    legend: {
      type: 'scroll',
      width: '70%',
      textStyle: {
        color: theme === 'dark' ? 'rgba(255, 255, 255, 1)' : 'rgba(110, 112, 121, 1)'
      },
      // left: 'left',
      data: val.legendData
    },
    xAxis: val.xAxisData,
    yAxis: val.yAxisData,
    series: val.seriesData
  }
  if (val.colors && val.colors.length > 0) {
    option.color = val.colors
  }
  return option
}
export default {
  name: 'BaseEcharts',
  props: {
    // 容器ID
    containerId: {
      type: String,
      default: 'echart-main'
    },
    colors: {
      type: Array,
      default: () => {
        return []
      }
    },
    tooltip: {
      type: Object,
      default: () => {
        return {
          trigger: 'axis',
          axisPointer: {
            type: 'cross'
          }
        }
      }
    },
    grid: {
      type: Object,
      default: () => {
        return {}
      }
    },
    legendData: {
      type: Array,
      default: () => {
        return []
      }
    },
    xAxisData: {
      type: Array,
      default: () => {
        return []
      }
    },
    yAxisData: {
      type: Array,
      default: () => {
        return []
      }
    },
    seriesData: {
      type: Array,
      default: () => {
        return []
      }
    },
    toolboxCfg: {
      type: Object,
      default: () => {
        return {
          dataViewIsShow: false,
          magicTypeIsShow: false,
          restoreIsShow: false,
          saveAsImageIsShow: false
        }
      }
    }
  },
  computed: {
    ...mapGetters(['theme']),
    option() {
      return opt(this.$props, this.theme)
    }
  },
  data() {
    return {
      myEchart: null,
      resizefun: null
    }
  },
  watch: {
    theme(nv, ov) {
      this.refreshEcharts()
    },
    seriesData: {
      handler(n, o) {
        this.refreshEcharts()
      },
      deep: true
    }
  },
  mounted() {
    this.refreshEcharts()
    this.resizefun = () => {
      this.myEchart.resize()
    }
    window.addEventListener('resize', this.resizefun)
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.resizefun)
    this.resizefun = null
  },
  methods: {
    refreshEcharts() {
      if (!this.myEchart) {
        const echartDom = document.getElementById(this.containerId)
        const myEchart = this.$echarts.init(echartDom, null)
        myEchart.setOption(this.option)
        this.myEchart = myEchart
        return false
      }
      this.myEchart.setOption(this.option)
    }
  }
}
</script>

<style></style>



