<script>
/**
 * @desc ranking component
 * @auth Demon3443002624@qq.com
 * @date 2022-7-14 15:54:06
 */

import _ from 'lodash'

export default {
  name: 'MyLegend',
  components: {},
  data() {
    return {
      chart: null,
      option: {},
      dataSetSort: [],
      colorLabel: '#fff',
      dialogVisible: false
    }
  },
  props: ['chartDataOriginal', 'all', 'top'],
  computed: {
    yAxisData() {
      return this.dataSetSort.map((e) => e.name)
    },
    seriesData() {
      return this.dataSetSort.map((e) => e.num)
    },
    id() {
      // DOM element id must to be complex
      return this.all ? 'ms-map-ranking-dialog' : 'ms-map-ranking'
    }
  },
  watch: {
    chartDataOriginal: {
      handler: function (nv) {
        // positive order
        this.dataSetSort = _.cloneDeep(nv).sort((a, b) => a.num - b.num)
        const l = this.dataSetSort.length
        !!this.top && this.dataSetSort.splice(0, l - this.top)
        this.getOption()
      },
      immediate: true
    }
  },
  methods: {
    getOption() {
      this.option = {
        grid: {
          left: 60,
          top: 40,
          height: '80%'
        },
        xAxis: {
          type: 'value',
          position: 'top',
          splitLine: {
            show: false
          },
          axisLabel: {
            height: 20,
            color: this.colorLabel
          },
          splitNumber: 4,
          max: (_) => {
            let max = _.max
            if (max <= 5) max = 5
            else if (max <= 10) max = 10
            else if (max <= 20) max = 20
            else if (max <= 50) max = 50
            else if (max <= 100) max = 100
            else if (max <= 200) max = 200
            else if (max <= 500) max = 500
            else max += 500
            return max
          }
        },
        yAxis: {
          type: 'category',
          position: 'left',
          axisLine: { show: true },
          axisLabel: {
            show: true,
            inside: false,
            overflow: 'truncate',
            width: 50,
            color: this.colorLabel
          },
          axisTick: { show: false },
          splitLine: { show: false },
          data: this.yAxisData
        },
        series: [
          {
            name: 'Cost',
            type: 'bar',
            itemStyle: {
              color: '#0FD1D9'
            },
            barMaxWidth: '50%',
            label: {
              show: true,
              position: 'right',
              color: this.colorLabel
            },
            data: this.seriesData
          }
        ]
      }
      try {
        this.chart ||
          (this.chart = this.$echarts.init(document.getElementById(this.id), null, {
            renderer: 'svg'
          }))
        this.chart.setOption(this.option)
      } catch (err) {
        // console.error(err)
      }
    }
  },
  beforeCreate() {},
  created() {},
  beforeMount() {},
  mounted() {
    this.getOption()
  },
  beforeUpdated() {},
  updated() {},
  beforeDestroy() {},
  destroyed() {}
}
</script>

<template>
  <div class="container" :id="id"></div>
</template>

<style lang="less" scoped>
.container {
  width: 100%;
  height: 100%;
}
</style>


